import React from 'react'
import { Button } from 'evergreen-ui'
import Carousel from 'react-multi-carousel'
import 'react-multi-carousel/lib/styles.css';
import styles from '../pages/HomePage/home.module.scss'
import { history } from "../../history";

const CategorySlider = () => {
    const responsive = {
        superLargeDesktop: {
            breakpoint: { max: 4000, min: 3000 },
            items: 16
        },
        desktop: {
            breakpoint: { max: 3000, min: 1024 },
            items: 10
        },
        tablet: {
            breakpoint: { max: 1024, min: 464 },
            items: 6
        },
        mobile: {
            breakpoint: { max: 464, min: 0 },
            items: 3
        }
    };
    const categories = ["Programming", "Network", "Marketing", "digital", "algorithm", "Politics", "Entertainment", "Sports", "Geography", "Religion", "stocks", "social", "program", "covid", "UN", "tips", "Trip"]
    return (
        <Carousel
            className={styles.carousel}
            responsive={responsive}
            swipeable={true}
            itemClass="carousel-item-padding-0-px"
        >
            {
                categories.map((item, index) => {
                    return (
                        <Button
                            color="neutral"
                            // value={item}
                            // key={index}
                            // onClick={(index) => { history.push(`/categorySelected/${index.target.value}`) }}
                            is="a" href={`/home/${item}`}
                            className={styles.badge}>
                            {item}
                        </Button>
                    )
                })
            }
        </Carousel>
    )
}
export default CategorySlider