import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { Paper, Chip } from '@material-ui/core'
import internalClasses from './style.module.css';
import { useSelector, connect } from 'react-redux'
import { getUser } from "../../../redux/actions/auth";
import { Redirect } from 'react-router-dom'
import { MoodRounded, AttachFileRounded, SendRounded } from '@material-ui/icons';
import { TextInput } from 'evergreen-ui';
import axios from 'axios';
import { getChatSpace } from "../../../utility/utils";
import { db } from '../../../services/chatfirebase';
import firebase from 'firebase';
import { proxy } from '../../../utility/proxy';
import BottomNavbar from '../../layout/BottomNavbar';
const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    height: '88vh'
  },
  chipBubble: {
    backgroundColor: '#fff',
    margin: '10px',
    fontSize: '14px'
  }
}));

function MessagesPage(props) {

  const classes = useStyles();

  var user = useSelector(state => state.user);
  const [userId, setUserId] = useState(user._id)
  const [chatPartnerId, setChatPartnerId] = useState(null)
  const [spaceId, setSpaceId] = useState(null)
  const [mySubscribe, setMySubscribe] = useState([])
  const [stateChatProp, setChatProp] = useState(null);
  const [chatSpaceName, setChatSpaceName] = useState("");
  const [input, setInput] = useState("");

  useEffect(() => {
    if (user && user._id) {
      axios.post(`${proxy}/api/mySubscribe`, { currentUserID: user._id })
        .then(res => {
          setMySubscribe(res.data.subscribe)
          setUserId(user._id)
        })
        .catch(err => {
          console.log(err);
        })
    }
  }, [user._id]);

  useEffect(async () => {
    if (spaceId) {
      db.collection('chats').doc(spaceId).onSnapshot(snapshot => {
        const ABC = snapshot.data()
        if(ABC) {
          const letter = Object.keys(ABC).find(key => ABC[key] === chatPartnerId)
          setChatSpaceName(snapshot.data()[`name${letter.slice(-1)}`]);
        }
      });

      db.collection('chats').doc(spaceId).collection("messages").orderBy("timestamp", "asc").onSnapshot(snapshot => {
        setChatProp(snapshot.docs.map(doc => doc.data()))
      });

    }
  }, [spaceId])
  if (!localStorage.getItem("token")) { return <Redirect to="/" /> }
  if (localStorage.getItem("token") && !user._id) {
    user = props.getUser(localStorage.getItem("token"));
  }

  const handleSelectChat = async (userId, value) => {
    if (value) setChatPartnerId(value._id)
    if (userId && value && value._id) {
      const chatDocId = await getChatSpace(userId, value._id)
      setSpaceId(chatDocId)
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    db.collection('chats').doc(spaceId).collection('messages').add({
      message: input,
      from: userId,
      to: chatPartnerId,
      dateTime: new Date(),
      timestamp: firebase.firestore.FieldValue.serverTimestamp(),
    })

    setInput("");
  }

  const notSelectedChat = <div style={{
    textAlign: 'center',
    fontSize: '20px',
    marginTop: '20px',
    width: "100%"
  }}>
    Subscribe your beautiful writers and Select a chat to start the conversation!
    </div>;
  return (
    <div className={internalClasses.container}>
      <BottomNavbar/>
      <List dense className={classes.root}>
        {mySubscribe && mySubscribe.map((value) => {
          return (
            <ListItem onClick={(e) => handleSelectChat(userId, value)} key={value._id} button>
              <ListItemAvatar>
                <Avatar
                  alt={`Avatar n${value.penname}`}
                  src={value.proilePic}
                />
              </ListItemAvatar>
              <h3>{value.penname}</h3>
            </ListItem>
          );
        })}
      </List>

      {stateChatProp ?
        <div className={internalClasses.chat}>
          <Paper elevation={0} className={internalClasses.paperbottom}>
            <MoodRounded style={{ fontSize: '28px', marginLeft: '10px' }} />
            <AttachFileRounded style={{ fontSize: '28px', marginLeft: '5px' }} />
            <TextInput
              name="text-input-name"
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="Type something..."
              style={{ width: '85%', borderRadius: '50px', marginLeft: '10px' }}
            />
            <SendRounded onClick={handleSubmit} style={{ fontSize: '28px', marginLeft: '15px' }} />
          </Paper>
          <div>
            {
              stateChatProp.map((element) => {
                return (
                  <div style={{ display: 'flex', justifyContent: element.from === userId ? `flex-end` : null }}>
                    {/* <span style={{ fontSize: "xx-small" }}>{new Date(element.timestamp?.toDate()).toUTCString()}</span> */}
                    <Chip
                      className={classes.chipBubble}
                      avatar={<Avatar alt="Natacha" src={element.from === chatPartnerId ? stateChatProp.image : user.proilePic} />}
                      label={element.message}
                      variant="outlined"
                    />
                  </div>
                );
              })}
          </div>
        </div> : notSelectedChat}
    </div>
  );
}
export default connect(null, { getUser })(MessagesPage);
