import React, { useState } from "react";
import axios from 'axios' 
import { makeStyles } from "@material-ui/core/styles";
import { proxy } from '../../../utility/proxy';
import {
  TextField,
  Button,
  Typography,
} from "@material-ui/core";
import BottomNavbar from "../../layout/BottomNavbar";
import { toaster } from "evergreen-ui";

const useStyles = makeStyles({
  labelStyles: {
    fontSize: "1.5rem",
    fontFamily: "Poppins, sans-serif",
  },
  textFieldStyles: {
    margin: "1.1rem auto",
    fontSize: "1.2rem",
    width: "30rem",
    "@media screen and (max-width:500px)": {
      width: "23rem",
    },
  },
  inputStyles: {
    fontSize: "1.2rem",
    fontFamily: "Poppins, sans-serif",
  },
  buttonStyles: {
    width: "100%",
    margin: "0 auto",
    fontSize: "1.4rem",
    textTransform: "lowercase",
  },
  wrapper: {
    height: "100%",
    width: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
  },
  title: {
    fontFamily: "Open Sans, sans-serif",
    textAlign: "center",
    color: "var(--black)",
    fontSize: "1.7rem",
    marginTop: "5rem",
    "@media screen and (max-width: 500px)": {
      fontSize: "1.4rem",
    },
  },
  container: {
    background: "#fff",
    padding: "1rem",
    height: "40rem",
    width: "50rem",
    marginTop: "2rem",
    borderRadius: "1rem",
    boxShadow: "0 1.5rem 2rem rgba(0, 0, 0, 0.1)",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    "@media screen and (max-width: 768px)": {
      width: "40rem",
      height: "37rem",
    },
    "@media screen and (max-width: 500px)": {
      width: "30rem",
      padding: "1.5rem",
    },
    "@media screen and (max-width: 350px)": {
      width: "25rem",
    },
  },
});
function FeedbackPage() {
  const [selectedOption, setSelectedOption] = useState("name");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const handleNameChange = (e) => {
    setName(e.target.value);
  };
  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };
  const handleMessageChange = (e) => {
    setMessage(e.target.value);
  };
  const classes = useStyles();

  const handleSubmit = () => {
    axios.post(`${proxy}/api/counsel-me` , { name , message , email , subject: 'feedback' })
    .then(res => { alert("Thanks for your feedback!!") }).catch(err => { 
      alert("Something went wrong!!")
    })
  } 

  return (
    <div className={classes.wrapper}>
      <BottomNavbar/>
      <h1 className={classes.title}>
        Your feedback really matter to us? 
      </h1>
      <div className={classes.container}>
        <form>
          {selectedOption === "name" && (
            <TextField
              id='outlined-helperText'
              label={
                <Typography className={classes.inputStyles}>
                  Your Name
                </Typography>
              }
              inputProps={{
                style: {
                  fontSize: "1.3rem",
                  fontFamily: "Poppins,sans-serif",
                },
              }}
              onChange={handleNameChange}
              value={name}
              defaultValue='Enter your name'
              variant='outlined'
              className={classes.textFieldStyles}
            />
          )}

          <TextField
            id='outlined-helperText'
            label={
              <Typography className={classes.inputStyles}>
                Your Email
              </Typography>
            }
            inputProps={{
              style: {
                fontSize: "1.3rem",
                fontFamily: "Poppins,sans-serif",
              },
            }}
            onChange={handleEmailChange}
            value={email}
            defaultValue='Enter your email'
            variant='outlined'
            className={classes.textFieldStyles}
          />
          <TextField
            multiline
            rows={4}
            id='outlined-helperText'
            label={
              <Typography className={classes.inputStyles}>Message</Typography>
            }
            inputProps={{
              style: {
                fontSize: "1.3rem",
                fontFamily: "Poppins,sans-serif",
              },
            }}
            onChange={handleMessageChange}
            value={message}
            defaultValue='Type your message'
            variant='outlined'
            className={classes.textFieldStyles}
          />
          <Button
            variant='contained'
            color='primary'
            className={classes.buttonStyles}
            onClick={handleSubmit}
          >
            Submit
          </Button>
        </form>
      </div>
    </div>
  );
}
export default FeedbackPage;
