import React, { useEffect, useState } from "react";
import axios from "axios";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import { useSelector } from "react-redux";
import { TextField, Button, Typography } from "@material-ui/core";
import { proxy } from "../../../utility/proxy";
import { Dialog, toaster } from "evergreen-ui";
import BottomNavbar from "../../layout/BottomNavbar";

const useStyles = makeStyles({
  labelStyles: {
    fontSize: "1.5rem",
    fontFamily: "Poppins, sans-serif",
  },
  textFieldStyles: {
    margin: "1.1rem auto",
    fontSize: "1.2rem",
    width: "30rem",
    "@media screen and (max-width:500px)": {
      width: "23rem",
    },
  },
  inputStyles: {
    fontSize: "1.2rem",
    fontFamily: "Poppins, sans-serif",
  },
  buttonStyles: {
    width: "100%",
    margin: "0 auto",
    fontSize: "1.4rem",
    textTransform: "lowercase",
  },
  wrapper: {
    height: "100%",
    width: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
  },
  title: {
    fontFamily: "Open Sans, sans-serif",
    textAlign: "center",
    color: "var(--black)",
    fontSize: "1.7rem",
    marginTop: "5rem",
    "@media screen and (max-width: 500px)": {
      fontSize: "1.4rem",
    },
  },
  container: {
    background: "#fff",
    padding: "1rem",
    minHeight: "90rem",
    width: "80rem",
    marginTop: "1rem",
    borderRadius: "1rem",
    boxShadow: "0 1.5rem 2rem rgba(0, 0, 0, 0.1)",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    "@media screen and (max-width: 768px)": {
      width: "50rem",
      height: "52rem",
    },
    "@media screen and (max-width: 500px)": {
      width: "30rem",
      padding: "1.5rem",
    },
    "@media screen and (max-width: 350px)": {
      width: "25rem",
    },
  },
});
function SettingsPage() {
  const userData = useSelector((state) => state.user);
  const [penname, setpenname] = useState("");
  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  const [checked, setChecked] = React.useState(true);
  const [linkedin, setlinkedin] = useState("");
  const [twitter, settwitter] = useState("");
  const [youtube, setyoutube] = useState("");
  const [instagram, setinstagram] = useState("");
  const [isShown, setIsShown] = React.useState(false)
  const [profileImage, setProfileImage] = useState(null);
  const loginUserId = useSelector((state) => state.user._id);
  let token = localStorage.getItem("token");

  useEffect(() => {
    let config = {
      headers: {
        "x-auth-token": token,
      },
    };
    axios.get(`${proxy}/api/get-user`, config).then((userData) => {
      if (userData.data.email) {
        setemail(userData.data.email);
      }
      if(userData.data.proilePic) {
        setProfileImage(userData.data.proilePic)
      }
      if (userData.data.penname) {
        setpenname(userData.data.penname);
      }
      if (userData.data.linkedin) {
        setlinkedin(userData.data.linkedin);
      }
      if (userData.data.twitter) {
        settwitter(userData.data.twitter);
      }
      if (userData.data.youtube) {
        setyoutube(userData.data.youtube);
      }
      if (userData.data.instagram) {
        setinstagram(userData.data.instagram);
      }
    });
  }, [token]);

  const handleChange = (event) => {
    setChecked(event.target.checked);
  };

  const handlePenChange = (e) => {
    setpenname(e.target.value);
  };

  const handleEmailChange = (e) => {
    setemail(e.target.value);
  };

  const handlePasswordChage = (e) => {
    axios.post(`${proxy}/api/send-forgot-mail`, {
      email: userData.email
    }).then(res => {
      toaster.success("Check your email box!"); 
    })
      .catch(err => {
        toaster.danger("Server issues!")
      });
  };

  const classes = useStyles();

  const handleSubmit = () => {
    if (penname && email) {
      let obj = {
        newPenname: penname,
        linkedin: linkedin,
        twitter: twitter,
        youtube: youtube,
        instagram: instagram,
        newPassword: password,
        newTheme: checked,
        newLanguage: "English",
        loginUserId: loginUserId,
      };
      if (!password) {
        delete obj.newPassword;
      }
      axios
        .post(`${proxy}/api/settings`, obj)
        .then((res) => { toaster.success("Updated Suuccessfully!"); })
        .catch((err) => { toaster.danger("Server issues!") });
    }
  };

  const updateImage = () => {
    if(profileImage) {
      const formData = new FormData();
      formData.append("email" , email)
      formData.append("image", profileImage)
    
      axios.patch(`${proxy}/api/updateProfileImage`, 
      formData, 
      { headers: {'Content-Type': 'multipart/form-data'}})
      .then(res => {
        toaster.success("Updated Suuccessfully!");
        setProfileImage(res.data.image)
        setIsShown(false)
      })
      .catch(err => toaster.danger("Server issues!"))
    }
  };

  return (
    <div className={classes.wrapper}>
      <Dialog
        isShown={isShown}
        title="Update profile image"
        onCloseComplete={() => setIsShown(false)}
        hasFooter={false}
      >
         <input
            id="profileImage"
            onChange={(e) => setProfileImage(e.target.files[0])}
            type="file" />
            <Button color="primary" onClick={updateImage}>Update</Button>
      </Dialog>


      <BottomNavbar />
      <h1 className={classes.title}>Update your settings</h1>
      <div className={classes.container}>
        <form>
          <Avatar
            onClick={() => setIsShown(true)}
            style={{ width: "70px", height: "70px", marginBottom: "10px" }}
            src={profileImage}
          />
          <h4>Email</h4>
          <TextField
            id="outlined-helperText"
            label={
              <Typography className={classes.inputStyles}>Email</Typography>
            }
            inputProps={{
              style: {
                fontSize: "1.3rem",
                fontFamily: "Poppins,sans-serif",
              },
            }}
            onChange={handleEmailChange}
            value={email}
            variant="outlined"
            className={classes.textFieldStyles}
          />
          <h4>Change Penname</h4>
          <TextField
            id="outlined-helperText"
            label={
              <Typography className={classes.inputStyles}>Penname</Typography>
            }
            inputProps={{
              style: {
                fontSize: "1.3rem",
                fontFamily: "Poppins,sans-serif",
              },
            }}
            onChange={handlePenChange}
            value={penname}
            variant="outlined"
            className={classes.textFieldStyles}
          />
          <h4>Social Links</h4>
          <TextField
            id="outlined-helperText"
            label={
              <Typography className={classes.inputStyles}>Linkedin</Typography>
            }
            inputProps={{
              style: {
                fontSize: "1.3rem",
                fontFamily: "Poppins,sans-serif",
              },
            }}
            onChange={(e) => setlinkedin(e.target.value)}
            value={linkedin}
            variant="outlined"
            className={classes.textFieldStyles}
          />
          <TextField
            id="outlined-helperText"
            label={
              <Typography className={classes.inputStyles}>Twitter</Typography>
            }
            inputProps={{
              style: {
                fontSize: "1.3rem",
                fontFamily: "Poppins,sans-serif",
              },
            }}
            onChange={(e) => settwitter(e.target.value)}
            value={twitter}
            variant="outlined"
            className={classes.textFieldStyles}
          />
          <TextField
            id="outlined-helperText"
            label={
              <Typography className={classes.inputStyles}>Youtube</Typography>
            }
            inputProps={{
              style: {
                fontSize: "1.3rem",
                fontFamily: "Poppins,sans-serif",
              },
            }}
            onChange={(e) => setyoutube(e.target.value)}
            value={youtube}
            variant="outlined"
            className={classes.textFieldStyles}
          />
          <TextField
            id="outlined-helperText"
            label={
              <Typography className={classes.inputStyles}>instagram</Typography>
            }
            inputProps={{
              style: {
                fontSize: "1.3rem",
                fontFamily: "Poppins,sans-serif",
              },
            }}
            onChange={(e) => setinstagram(e.target.value)}
            value={instagram}
            variant="outlined"
            className={classes.textFieldStyles}
          />
          <h4 style={{ fontSize: 16, cursor: 'pointer', margin: 10 }} onClick={handlePasswordChage}>Change Password</h4>

          <Button
            variant="contained"
            color="primary"
            className={classes.buttonStyles}
            onClick={handleSubmit}
            disabled={!penname || !email}
          >
            Save
          </Button>
        </form>
      </div>
    </div>
  );
}
export default SettingsPage;
