import React from "react";
import { Pane, Badge, Heading } from "evergreen-ui";
import styles from "../pages/HomePage/home.module.scss";
import { Button } from "evergreen-ui";

const Tags = () => {
  const categories = [
    "Programming",
    "Marketing",
    "Politics",
    "Entertainment",
    "Sports",
    "program",
    "Trip",
  ];

  return (
    <Pane className={styles.tags}>
      <Heading size={600} marginTop={4} marginBottom={12}>
        Tags
      </Heading>
      <Pane>
        {categories.map((item, index) => {
          return (
            <Button
              color="neutral"
              // value={item}
              // key={index}
              // onClick={(index) => { history.push(`/categorySelected/${index.target.value}`) }}
              is="a"
              href={`/home/${item}`}
              className={styles.badge}
            >
              {item}
            </Button>
          );
        })}
        {/* <Badge color="neutral" className={styles.badge}> */}
        {/* </Badge> */}
        {/* <Badge color="neutral" className={styles.badge}>
          Marketing
        </Badge>
        <Badge color="neutral" className={styles.badge}>
          Development
        </Badge>
        <Badge color="neutral" className={styles.badge}>
          Hoisting
        </Badge>
        <Badge color="neutral" className={styles.badge}>
          work
        </Badge>
        <Badge color="neutral" className={styles.badge}>
          salary
        </Badge>
        <Badge color="neutral" className={styles.badge}>
          programming
        </Badge> */}
      </Pane>
    </Pane>
  );
};

export default Tags;
