import React, { useState } from "react";
import useStyles from "./makeStyles";
import {
  Stepper,
  Step,
  StepButton,
  Button,
  Typography,
  TextField,
  Container,
  FormControl,
  InputLabel,
} from "@material-ui/core";
import { Editor } from "react-draft-wysiwyg";
import { EditorState, convertToRaw } from "draft-js";
import draftToHtml from "draftjs-to-html";
import axios from "axios";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import styles from "./publish.module.css";
import { getUser } from "../../../redux/actions/auth";
import { connect, useSelector } from "react-redux";
import jwt from "jsonwebtoken";
import { toaster } from 'evergreen-ui'

import { makeStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";
import { proxy } from "../../../utility/proxy";
import BottomNavbar from "../../layout/BottomNavbar";
const useStyles1 = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));
function PublishPage(props) {
  //all states
  const classes1 = useStyles1();
  const [heading, setHeading] = useState("");
  const [category, setCategory] = useState("");
  const [selectedImage, setSelectedImage] = useState(null);
  const [description, setDescription] = useState("");
  const [editorState, setEditorState] = useState(EditorState.createEmpty());
  const [activeStep, setActiveStep] = useState(0);
  const [completed, setCompleted] = useState(new Set());
  const [skipped, setSkipped] = useState(new Set());
  const [imagetoupload, setImagetoUpload] = useState(null);
  const categoryList = [
    "Programming",
    "Network",
    "Marketing",
    "digital",
    "algorithm",
    "Politics",
    "Entertainment",
    "Sports",
    "Geography",
    "Religion",
    "stocks",
    "social",
    "program",
    "covid",
    "UN",
    "tips",
    "Trip",
  ];

  const classes = useStyles();
  const steps = getSteps();
  const handleChangeCategory = (event) => {
    setCategory(event.target.value);
  };
  //get the current signed in user
  const currentUserId = useSelector((state) => state.user._id);
  if (localStorage.getItem("token") && !currentUserId) {
    props.getUser(localStorage.getItem("token"));
  }
  //send blog data to backend
  const handlePublish = () => {
    handleComplete();

    const formData = new FormData();
    formData.append("id" , currentUserId)
    formData.append("heading", heading)
    formData.append("category", category)
    formData.append("description", description)
    formData.append("image", imagetoupload)
    formData.append("publishedDate", new Date())
    formData.append("archived" , false)
  
    axios.post(`${proxy}/api/publish`, 
    formData, 
    { headers: {'Content-Type': 'multipart/form-data'}})
    .then(res => {
      toaster.success("Awesome!");
      props.history.push("/home");
    })
    .catch(err => toaster.danger("Server issues!"))

    // axios.post(`${proxy}/api/publish`, publishData).then((res) => {
    //   console.log(res);
    //   if (res && res.data && res.data.success) props.history.push("/home");
    // });
  };

  /*---------------all the handlers related to steps are started from here-------------------*/
  const totalSteps = () => {
    return getSteps().length;
  };

  const isStepOptional = (step) => {
    return step === 1;
  };

  const handleSkip = () => {
    if (!isStepOptional(activeStep)) {
      throw new Error("You can't skip a step that isn't optional.");
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped((prevSkipped) => {
      const newSkipped = new Set(prevSkipped.values());
      newSkipped.add(activeStep);
      return newSkipped;
    });
  };

  const skippedSteps = () => {
    return skipped.size;
  };

  const completedSteps = () => {
    return completed.size;
  };

  const allStepsCompleted = () => {
    return completedSteps() === totalSteps() - skippedSteps();
  };

  const isLastStep = () => {
    return activeStep === totalSteps() - 1;
  };

  const handleNext = () => {
    const newActiveStep =
      isLastStep() && !allStepsCompleted()
        ? steps.findIndex((step, i) => !completed.has(i))
        : activeStep + 1;

    setActiveStep(newActiveStep);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStep = (step) => () => {
    console.log(step);
    setActiveStep(step);
  };

  const handleComplete = () => {
    const newCompleted = new Set(completed);
    newCompleted.add(activeStep);
    setCompleted(newCompleted);

    if (completed.size !== totalSteps() - skippedSteps()) {
      handleNext();
    }
  };

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  function isStepComplete(step) {
    return completed.has(step);
  }

  function getSteps() {
    return ["Heading", "Category", "Image", "Description", "Preview"];
  }

  const getStepContent = (step) => {
    switch (step) {
      case 0:
        return (
          <TextField
            id="outlined-basic"
            label={"Heading, please make sure to save everything"}
            variant="outlined"
            value={heading}
            onChange={handleHeadingChange}
          />
        );
      case 1:
        return (
          <FormControl variant="outlined" className={classes1.formControl}>
            <InputLabel id="demo-simple-select-outlined-label">
              Category
            </InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="outlined-basic"
              value={category}
              className="px-10"
              onChange={handleChangeCategory}
              label="Category"
            >
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
              {categoryList.map((data) => {
                return <MenuItem value={data}>{data}</MenuItem>;
              })}
            </Select>
          </FormControl>
        );
      case 2:
        return (
          <input type="file" accept="image/*" onChange={handleImageChange} />
        );
      case 3:
        return (
          <Editor
            editorState={editorState}
            toolbarClassName="toolbarClassName"
            wrapperClassName="wrapperClassName"
            editorClassName="editorClassName"
            onEditorStateChange={handleEditorStateChange}
            editorStyle={{
              minHeight: "35rem"
            }}
          />
        );
      case 4:
        return (
          <div className={styles.previewContainer}>
            <h1 className={styles.previewHeading}>{heading}</h1>
            <img
              src={selectedImage}
              alt={selectedImage}
              className={styles.previewImage}
            />
            <div
              dangerouslySetInnerHTML={{ __html: description }}
              className={styles.previewDescription}
            />
            <h5 className={styles.previewCategory}>
              {category === "" ? "" : `Category: ${category}`}
            </h5>
          </div>
        );
      default:
        return "Unknown step";
    }
  };

  /*---------------all the handlers related to steps are done till here-------------------*/

  //handle heading change
  const handleHeadingChange = (e) => {
    setHeading(e.target.value);
  };

  //handle category change

  //handle image change
  const handleImageChange = async (e) => {
    const image = await convertToBase64(e.target.files[0]);
    setSelectedImage(image);
    setImagetoUpload(e.target.files[0])
  };

  //function to convert to base 64
  const convertToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };

  //handle editor state change and set the description
  const handleEditorStateChange = (editorState) => {
    setEditorState(editorState);
    const getText = draftToHtml(convertToRaw(editorState.getCurrentContent()));
    setDescription(getText);
  };

  return (
    <div className={classes.root}>
      <BottomNavbar/>
      <Stepper
        className={classes.stepper}
        alternativeLabel
        nonLinear
        activeStep={activeStep}
      >
        {steps.map((label, index) => {
          const stepProps = {};
          const buttonProps = {};
          // if (isStepOptional(index)) {
          //   buttonProps.optional = (
          //     <Typography variant="caption">Optional</Typography>
          //   );
          // }
          if (isStepSkipped(index)) {
            stepProps.completed = false;
          }
          return (
            <Step key={label} {...stepProps}>
              <StepButton
                onClick={handleStep(index)}
                completed={isStepComplete(index)}
                {...buttonProps}
              >
                {label}
              </StepButton>
            </Step>
          );
        })}
      </Stepper>
      <div>
        {allStepsCompleted() ? (
          <div>
            <Typography
              className={classes.instructions}
              style={{
                fontSize: "3rem",
                fontFamily: "Nunito, sans-serif",
                marginTop: "2rem",
                textAlign: "center",
              }}
            >
              Your awesome article was successfully published!
            </Typography>
          </div>
        ) : (
          <Container
            className={
              activeStep === 3 ? classes.editorStep : classes.stepContent
            }
            style={{ padding: "10rem 0rem" }}
            maxWidth="md"
          >
            <div
              className={
                activeStep === 3
                  ? classes.editorStepInstructions
                  : classes.instructions
              }
            >
              {getStepContent(activeStep)}
            </div>
            <div>
              <Button
                disabled={activeStep === 0}
                onClick={handleBack}
                className={classes.button}
              >
                Back
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={handleNext}
                className={classes.button}
              >
                Next
              </Button>
              {isStepOptional(activeStep) && !completed.has(activeStep) && (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleSkip}
                  className={classes.button}
                >
                  Skip
                </Button>
              )}

              {activeStep !== steps.length &&
                (completed.has(activeStep) ? (
                  <Typography variant="caption" className={classes.completed}>
                    Step {activeStep + 1} already completed
                  </Typography>
                ) : completedSteps() === totalSteps() - 1 ? (
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handlePublish}
                  >
                    Publish
                  </Button>
                ) : (
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleComplete}
                  >
                    Save
                  </Button>
                ))}
            </div>
          </Container>
        )}
      </div>
    </div>
  );
}
export default connect(null, { getUser })(PublishPage);
