import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { AiFillCheckCircle } from "react-icons/ai";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { proxy } from '../../../utility/proxy';
import { getUser } from "../../../redux/actions/auth";
import axios from "axios";
import BottomNavbar from "../../layout/BottomNavbar";

const NotificationWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: "Poppins";
  background-color: #fff;
  margin: 10rem;
  padding: 0 4rem 1rem 4rem;
  border-radius: 1rem;
  box-shadow: 0rem 1.5rem 1.5rem rgba(0, 0, 0, 0.1);
  color: var(--black);
  @media screen and (max-width: 600px) {
    margin: 10rem 3rem 10rem 0rem;
  }
  @media screen and (min-width: 800px) {
    margin: 10rem 20rem;
  }
`;

const NotificationHeader = styled.h1`
  font-size: 2.5rem;
  border-bottom: 0.2rem solid rgba(0, 0, 0, 0.2);
  @media screen and (max-width: 600px) {
    font-size: 2rem;
  }
`;
const NotificationCard = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.6rem;
  margin-bottom: 1rem;
  font-family: "Nunito", sans-serif;
  @media screen and (max-width: 600px) {
    font-size: 1.4rem;
  }
`;

const MarkAsReadIcon = styled.div`
  font-size: 2.5rem;
  color: var(--lighter-grey);
  &:hover {
    color: var(--dark-blue);
    cursor: pointer;
    transition: all 0.2s;
  }
`;
const NotificationsPage = (props) => {
  const [isLoading, setIsLoading] = useState(false);
  const [notifications, setNotifications] = useState([]);

  useEffect(() => {
    if (props && props.user && props.user._id) {
      axios
        .post(`${proxy}/api/notifications`, {
          userId: props.user._id,
        })
        .then((results) => {
          setNotifications(results.data);
        });
    }
  }, [props]);
  console.log(notifications);
  if (!localStorage.getItem("token")) {
    return <Redirect to="/" />;
  }
  if (localStorage.getItem("token") && !props.user._id) {
    props.getUser(localStorage.getItem("token"));
  }

  return (
    <>
      <BottomNavbar/>
        <NotificationWrapper>
          <NotificationHeader>Notifications</NotificationHeader>
          {notifications && notifications.length > 0
            ? notifications.map((data, index) => {
                return (
                  <NotificationCard key={index}>
                    {data.message}
                    <MarkAsReadIcon>
                      <AiFillCheckCircle />
                    </MarkAsReadIcon>
                  </NotificationCard>
                );
              })
            : ""}
        </NotificationWrapper>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};
export default connect(mapStateToProps, { getUser })(NotificationsPage);
