import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import BottomNavbar from '../../layout/BottomNavbar';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: '25px',
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  para: {
    fontSize: '20px',
  }
}));

export default function HelpPage() {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className={classes.root}>
      <BottomNavbar/>
      <h1 style={{ fontSize: '30px' , margin: '10px' }}>Help</h1>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className={classes.heading}>General settings</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className={classes.para}>
            Welcome To Glexpace. Glexpace is as simple as the way you write or read. Just Publish an article, others will read. If your article is being loved by lots of people, your account will be ready to earn. So Publish amazing and beautiful articles, get loved and seen by millions. Soon we will send you notifications to connect with people who are capable to think the way you think. Stick to us and get the oppurtunity to meet your peeps. Also soon we will edit your stories and you will be able to put your own voice with background music, so your readers will be able to feel the emotions you want to express.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography className={classes.heading}>Users</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className={classes.para}>
           You are the most important human in this planet. Writing makes you a human filled with abilities. If you are able to write and educate, you are making people to learn, and if someone learns because of you, you are making this world a better place. Being a member of Glexpace Family, you will recieve monthly presents if you are able to make more and more people learn. So get set Go. We are with you.
          </Typography>
        </AccordionDetails>
      </Accordion>
      
        
        
      <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography className={classes.heading}>Personal data</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className={classes.para}>
            All your data are safe, and will never be shared to third party, for any query feel free to ping us.
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}