import React from 'react'
import { AiFillInstagram, AiOutlineTwitter } from 'react-icons/ai';
import { FaFacebookSquare, FaLinkedin } from "react-icons/fa";
import { Pane } from 'evergreen-ui'
import styles from '../pages/HomePage/home.module.scss'

const SocialProfiles = () => {
    return (
        <Pane display="flex" className={styles.socialProfiles}>
            <AiFillInstagram style={{ fontSize: '25px', marginLeft: 15 }} />
            <FaLinkedin style={{ fontSize: '25px', marginLeft: 15 }} />
            <FaFacebookSquare style={{ fontSize: '25px', marginLeft: 15 }} />
            <AiOutlineTwitter style={{ fontSize: '25px', marginLeft: 15 }} />
        </Pane>
    )
}

export default SocialProfiles;