import React, { useState } from "react";
import "./BookmarkStyles.css";
import { MdDeleteForever } from "react-icons/md";
import { Pagination } from "@material-ui/lab";

function BookmarksPage() {
  const [bookmarks, setBookmarks] = useState([
    {
      blogImgUrl: "laptop-network.jpg",
      category: "Networking",
      title: "Barista and sams hjhj hkhj jkhkh ",
      authorImgUrl: "profile.png",
      authorName: "John Doe",
      publishedDate: "Aug 12, 2020",
    },
    {
      blogImgUrl: "laptop-network.jpg",
      category: "Science",
      title: "Sometitle abcdefg hkhkh",
      authorImgUrl: "profile.png",
      authorName: "Some name",
      publishedDate: "Aug 15, 2020",
    },
    {
      blogImgUrl: "laptop-network.jpg",
      category: "Science",
      title: "Sometitle abcdefg hkhkh",
      authorImgUrl: "profile.png",
      authorName: "Some name",
      publishedDate: "Aug 15, 2020",
    },
    {
      blogImgUrl: "laptop-network.jpg",
      category: "Science",
      title: "Sometitle abcdefg hkhkh",
      authorImgUrl: "profile.png",
      authorName: "Some name",
      publishedDate: "Aug 15, 2020",
    },
    {
      blogImgUrl: "laptop-network.jpg",
      category: "Science",
      title: "Sometitle abcdefg hkhkh",
      authorImgUrl: "profile.png",
      authorName: "Some name",
      publishedDate: "Aug 15, 2020",
    },
  ]);
  const [currentPage, setCurrentPage] = useState(1);
  const [bookmarksPerPage] = useState(3);

  const indexOfLastBookmark = currentPage * bookmarksPerPage;
  const indexOfFirstBookmark = indexOfLastBookmark - bookmarksPerPage;
  const currentBookmarks = bookmarks.slice(
    indexOfFirstBookmark,
    indexOfLastBookmark
  );

  const paginate = (e,page) => {
    setCurrentPage(e);
  };
  return (
    <div className='wrapper'>
      <h1 className='title'>Bookmarks</h1>
      {currentBookmarks.map((item, index) => {
        return (
          <div key={index} className='bookmark-card'>
            <div className='blog-image'>
              <img src={item.blogImgUrl} alt={item.category} />
            </div>
            <div className='info'>
              <div className='blog-header'>
                <p>{item.category}</p>
                <h3>{item.title}</h3>
              </div>
              <div className='author-info'>
                <img src={item.authorImgUrl} alt='' />
                <div className='name-and-date'>
                  <p>{item.authorName}</p>
                  <p>{item.publishedDate}</p>
                </div>
              </div>
            </div>
            <div className='delete-icon'>
              <MdDeleteForever />
            </div>
          </div>
        );
      })}
      {bookmarks.length > 3 && (
        <Pagination
          count={Math.ceil(bookmarks.length / bookmarksPerPage)}
          color='primary'
          className='pagination'
          onChange={(e, page) => paginate(page)}
        />
      )}
    </div>
  );
}
export default BookmarksPage;
