import React, { useEffect } from "react";
import parse from 'html-react-parser'
import { connect, useSelector } from "react-redux";
import CategorySlider from "../../reusableComponents/CategorySlider";
import { getUser } from "../../../redux/actions/auth";
import { Grid } from "@material-ui/core";
import PostInsights from "../../reusableComponents/PostInsights";
import SocialProfiles from "../../reusableComponents/SocialProfiles";
import Tags from "../../reusableComponents/Tags";
import styles from "../HomePage/home.module.scss";
import poststyles from "./postdetails.module.scss";
import { makeStyles } from "@material-ui/core/styles";
import { TextField, Button } from "@material-ui/core";
import { Heading, Pane, Text, Badge } from "evergreen-ui";
import axios from "axios";
import {fireBase} from "../../../services/chatfirebase";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { proxy } from '../../../utility/proxy';
import BottomNavbar from "../../layout/BottomNavbar";
import { Link } from "react-router-dom";
const moment = require("moment");

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "flex-start",
    justifyContent: "center",
    "& .MuiTextField-root": {
      margin: "2rem 0",
      width: "40rem",
      "@media screen and (max-width:500px)": {
        width: "30rem",
      },
    },
    "& button": {
      fontSize: "1.3rem",
    },
    "& label": {
      fontSize: "1.5rem",
      fontFamily: "Poppins, sans-serif",
    },
    "& textarea": {
      margin: "1.1rem auto",
      fontSize: "1.2rem",
      width: "100%",
      fontFamily: "Poppins, sans-serif",
    },
    "& fieldset": {
      "& span": {
        padding: "0 4rem",
      },
    },
  },
}));

const PostdetailsByIdPage = (props) => {

  var user = useSelector(state => state.user);
  const [userId, setUserId] = React.useState(user._id)
  const [open, setOpen] = React.useState(false);
  const [refresh, setRefresh] = React.useState(false);
  const [reply, setReply] = React.useState("");
  const [blogData, setBlogData] = React.useState({});
  const [blodIdToFetch, setBlodIdToFetch] = React.useState(
    props.history.location.pathname.split("/")[
    props.history.location.pathname.split("/").length - 1
    ]
  );
  const [comments, setComments] = React.useState({});
  const [currentCommentId, setCurrentCommentId] = React.useState(0);
  const [featuredPost, setFeaturedPost] = React.useState([]);

  useEffect(() => {
    const len = props.history.location.pathname.split("/").length - 1;
    setBlodIdToFetch(props.history.location.pathname.split("/")[len]);
    axios
      .get(`${proxy}/api/post-details/${blodIdToFetch}`)
      .then((res) => {
        if (res && res.data) setBlogData(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
    fireBase.database().ref(`/comments/${blodIdToFetch}`).once('value', (snapshot) => {
        const result = snapshot.val()
        setComments(result)
    })

    axios.get(`${proxy}/api/random`).then(res => {
      res && res.data && setFeaturedPost(res.data)
    })
    .catch(err => { console.log(err)} )
  }, [props, refresh]);
  if (localStorage.getItem("token") && !props.user._id) {
    props.getUser(localStorage.getItem("token"));
  }
  const classes = useStyles();
  const [value, setValue] = React.useState("");

  const handleChange = (event) => {
    setValue(event.target.value);
  };

  const handleSubmit = (e) => {
    const id = blodIdToFetch;
    e.preventDefault();
    const commentID = Math.floor(Math.random() * 100000000);
    fireBase.database().ref(`comments/${id}/${commentID}`).set({
      userID: user._id,
      comment: value,
      commentID,
    });
    window.location.reload();
    setValue("");
  };
  const handleReplyChange = (e) => {
    setReply(e.target.value);
  };
  const handleReply = () => {
    fireBase
      .database()
      .ref(`comments/${blodIdToFetch}/${currentCommentId}/replies`)
      .push({
        userID: user._id,
        reply: reply,
      });
    setReply("");
    window.location.reload();
    handleClose();
  };

  const handleClickOpen = (id) => {
    setCurrentCommentId(id);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleLikeClick = (props, userId) => {
    axios
      .put(`${proxy}/api/like`, { _id: props._id, userId })
      .then((res) => {
        if (res) setRefresh(!refresh);
      });
  };
  const handleUnLikeClick = (props, userId) => {
    axios
      .put(`${proxy}/api/unlike`, { _id: props._id, userId })
      .then((res) => {
        if (res) setRefresh(!refresh);
      });
  };

  const doUnBookmark = (props, userId) => {
    axios
      .put(`${proxy}/api/unSaveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) setRefresh(!refresh);
      });
  };
  const doBookmark = (props, userId) => {
    axios
      .put(`${proxy}/api/saveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) setRefresh(!refresh);
      });
  };
  return (
    <>
    <BottomNavbar/>
      <CategorySlider />
      <Grid container>
        <Grid container item lg={8} md={12} className={styles.postsGrid}>
          <Grid item lg={12} className={styles.profileGrid}>
            <Heading size={700} padding={20}>
              {blogData && blogData.heading ? blogData.heading : "-"}
            </Heading>
            <Pane className={poststyles.blogCard}>
              <Pane className={poststyles.blogImageDiv}>
                <img
                  src={blogData.image}
                  alt=""
                  className={poststyles.blogImage}
                />
                <Badge color="neutral" className={poststyles.badge}>
                  {blogData && blogData.category ? blogData.category : "-"}
                </Badge>
              </Pane>
              <Pane className={poststyles.insights}>
                <PostInsights
                  res={blogData}
                  userId={props.user._id}
                  doBookmark={doBookmark}
                  doUnBookmark={doUnBookmark}
                  onLikeClick={handleLikeClick}
                  onUnLikeClick={handleUnLikeClick}
                  likes={blogData.likes ? blogData.likes : 0}
                  saves={blogData.saves ? blogData.saves : 0}
                />
                <Badge color="neutral" className={styles.badge}>
                  {blogData && blogData.publishedDate
                    ? moment(blogData.publishedDate).format("DD, MMM YYYY")
                    : "-"}
                </Badge>
              </Pane>
              <Pane className={poststyles.description}>
                {blogData && blogData.description ? parse(`${blogData.description}`) : "-"}
              </Pane>
              <Pane className={poststyles.authorAndDate}>
                <Pane className={poststyles.authorIntro}>
                  <img
                    src={
                      blogData &&
                      blogData.author &&
                      blogData.author.proilePic &&
                      blogData.author.proilePic
                    }
                    alt=""
                    className={poststyles.authorImage}
                  />
                  <Text size={300} paddingLeft={6}>
                    {blogData && blogData.author && blogData.author.penname
                      ? blogData.author.penname
                      : "-"}
                  </Text>
                </Pane>
              </Pane>
            </Pane>
            <Pane className={poststyles.commentSection}>
              <Heading size={700}>
                Comments 
              </Heading>
              <form
                className={classes.root}
                noValidate
                autoComplete="off"
                onSubmit={handleSubmit}
              >
                <TextField
                  id="outlined-multiline-flexible"
                  label="Add Comment"
                  multiline
                  rows={6}
                  value={value}
                  onChange={handleChange}
                  variant="outlined"
                />
                <Button onClick={handleSubmit} variant="contained" color="primary" type="submit">
                  Submit
                </Button>
              </form>
              {comments &&
                Object.entries(comments).map((item, index) => {
                  return (
                    <Pane className={poststyles.commentDiv} key={index}>
                      <img src="https://ov-cms.s3.ap-south-1.amazonaws.com/B2B/AdminPanel/CMS/AGY427/Images/09102019022455.png" alt="" />
                      <Pane className={poststyles.commentContent}>
                        <Pane className={poststyles.commentor}>
                          <Text size={300}>Glexpace user</Text>
                        </Pane>
                        <Pane className={poststyles.commentText}>
                          <Text size={400}>{item[1].comment}</Text>
                          {/* <Button
                            onClick={() => handleClickOpen(item[1].commentID)}
                            color="primary"
                          >
                            Reply
                          </Button> */}
                        </Pane>
                      </Pane>
                    </Pane>
                  );
                })}
            </Pane>
          </Grid>
        </Grid>
        <Grid container item lg={4} md={12} className={poststyles.recentposts}>
          <Heading size={700} marginTop={25}>
            Recent Posts
          </Heading>
          {
            featuredPost.length ? 
                <Pane className={poststyles.postcard}>
                {console.log(featuredPost)}
                <img
                  src={featuredPost[0].image}
                  alt=""
                  className={poststyles.recentPostImg}
                />
                <Pane className={poststyles.postCardDiv}>
                  <Text size={500} color="#ffffff">
                   <Link to={`/postdetails/${featuredPost[0]._id}`}>{featuredPost[0].heading}</Link> 
                  </Text>
                  <Text size={300} marginTop={10} color="#ffffff">
                  { moment(featuredPost[0].publishedDate).format("DD, MMM YYYY") }
                  </Text>
                </Pane>
              </Pane>
            : <p>No featured post for now</p>
          }
       
          {/* <SocialProfiles /> */}
          <Tags />
        </Grid>
      </Grid>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Use Google's location service?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <TextField
              id="outlined-multiline-static"
              label="Add your reply"
              multiline
              rows={4}
              variant="outlined"
              value={reply}
              onChange={handleReplyChange}
            />
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleReply} color="primary" autoFocus>
            Reply
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};
export default connect(mapStateToProps, { getUser })(PostdetailsByIdPage);
