import React, { useEffect, useState } from "react";
import { HiSpeakerphone } from "react-icons/hi";
import { FaHome, FaUser, FaBell, FaBookmark } from "react-icons/fa";
import { BsChatFill } from "react-icons/bs";
import { MdEmail, MdSettings, MdHelp, MdFeedback } from "react-icons/md";
import { IoLogOut } from "react-icons/io5";
import toggleAction from "../../redux/actions/toggleAction";
import {
  SidebarWrapper,
  SidebarItem,
  SidebarItemIcon,
  SidebarItemName,
  SidebarVerticalStrip,
  Backdrop,
} from "../../styling/SidebarStyles";
import { connect, useSelector } from "react-redux";
import { Link } from "react-router-dom";
const Sidebar = (props) => {
  const tempShow = useSelector((state) => state.showSidebar.show);
  const [show, setshow] = useState(tempShow);
  useEffect(() => {
    setshow(tempShow);
  }, [tempShow]);

  // if (!localStorage.getItem("token")) setshow(false)

  const [sidebarItems, setSidebarItems] = useState([
    {
      icon: <FaHome />,
      name: "Home",
      path: "/home",
    },
    {
      icon: <HiSpeakerphone />,
      name: "Publish",
      path: "/publish",
    },
    {
      icon: <FaUser />,
      name: "Profile",
      path: "/profile",
    },
    {
      icon: <BsChatFill />,
      name: "Message",
      path: "/messages",
    },
    {
      icon: <FaBell />,
      name: "Notification",
      path: "/notifications",
    },
    {
      icon: <MdEmail />,
      name: "Counsel Me",
      path: "/counselme",
    },
    {
      icon: <MdSettings />,
      name: "Settings",
      path: "/settings",
    },
    {
      icon: <MdHelp />,
      name: "Help",
      path: "/help",
    },
    {
      icon: <MdFeedback />,
      name: "Feedback",
      path: "/feedback",
    },
    {
      icon: <IoLogOut />,
      name: "Logout",
      path: "/logout",
    },
  ]);
  return (
    <Backdrop
      show={show}
      onClick={() => {
        props.toggleAction(false);
      }}
    >
      <SidebarWrapper show={show}>
        <SidebarVerticalStrip></SidebarVerticalStrip>
        {sidebarItems.map((item, index) => {
          return (
            <Link style={{ textDecoration: "none" }} key={index} to={item.path}>
              <SidebarItem>
                <SidebarItemIcon>{item.icon}</SidebarItemIcon>
                <SidebarItemName>{item.name}</SidebarItemName>
              </SidebarItem>
            </Link>
          );
        })}
      </SidebarWrapper>
    </Backdrop>
  );
};

export default connect(null, { toggleAction })(Sidebar);
