import React, { useState, useEffect } from "react";
import parse from "html-react-parser";
import {} from "../../../../utils/css/tailwind.css";
import { useSelector } from "react-redux";
import axios from "axios";
import styles from "../../HomePage/home.module.scss";
import { Bookmark, Visibility, ThumbUpAlt } from "@material-ui/icons";
import {
  Pane,
  Heading,
  Text,
  Spinner,
  Button,
  Paragraph,
  Badge,
} from "evergreen-ui";
import { Link } from "react-router-dom";
import PostInsights from "../../../reusableComponents/PostInsights";
import { proxy } from "../../../../utility/proxy";
const moment = require("moment");

function Bookmarks(props) {
  const user = useSelector((state) => state.user);
  const [bookmarks, setbookmarks] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [change, handleChange] = useState(true);
  useEffect(() => {
    setIsLoading(true);
    axios
      .post(`${proxy}/api/bookmark`, { currentUserId: user._id })
      .then((res) => {
        setbookmarks(res.data);
        setIsLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [user, change]);

  const handleLikeClick = (props, userId) => {
    setIsLoading(true);
    axios.put(`${proxy}/api/like`, { _id: props._id, userId }).then((res) => {
      if (res) handleChange(!change);
      setIsLoading(false);
    });
  };
  const handleUnLikeClick = (props, userId) => {
    setIsLoading(true);
    axios.put(`${proxy}/api/unlike`, { _id: props._id, userId }).then((res) => {
      if (res) handleChange(!change);
      setIsLoading(false);
    });
  };

  const doUnBookmark = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/unSaveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };
  const doBookmark = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/saveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };

  const increaseView = (props) => {
    axios.put(`${proxy}/api/increaseView`, { _id: props._id }).then((res) => {
      if (res) handleChange(!change);
    });
  };
  return (
    <div style={{ margin: "0px 40px" }}>
      {isLoading === true ? (
        <Pane
          display="flex"
          alignItems="center"
          justifyContent="center"
          height={600}
        >
          <Spinner color="primary" marginX="auto" />
        </Pane>
      ) : (
        <div>
          {bookmarks.map((ele, i) => {
            let trim = ele.description.split(" ");
            trim.length = 30;
            trim = trim.join(" ");
            return (
              <Pane key={i} className={styles.card}>
                <Pane className={styles.cardImage}>
                  <img
                    className="img-fluid"
                    className={styles.blogImage}
                    src={ele.image}
                    alt=""
                  />
                  <Badge color="neutral" className={styles.cardCategory}>
                    {ele.category}
                  </Badge>
                </Pane>
                <Pane className={styles.cardDescription}>
                  <Badge color="neutral" className={styles.badge}>
                    {ele && ele.publishedDate
                      ? moment(ele.publishedDate).format("DD, MMM YYYY")
                      : "-"}
                  </Badge>
                  <Heading size={600}>
                    {ele && ele.heading ? ele.heading : "-"}
                  </Heading>
                  <Paragraph size={300}>
                    {ele && ele.description ? parse(`${trim}`) : "-"}
                  </Paragraph>
                  <Link
                    to={`/postdetails/${ele._id}`}
                    className={styles.cardButton}
                    onClick={() => increaseView(ele)}
                  >
                    Read More
                  </Link>
                  <Pane className={styles.cardInfo}>
                    <Pane className={styles.cardAuthor}>
                      <img
                        src={
                          ele && ele.author && ele.author.proilePic
                            ? ele.author.proilePic
                            : "https://playuganda.com/wp-content/uploads/2020/12/profile-pic.jpg"
                        }
                        alt=""
                      />
                      <Text size={300} paddingLeft={6}>
                        {ele.author && ele.author.penname
                          ? ele.author.penname
                          : "-"}
                      </Text>
                    </Pane>
                    <PostInsights
                      res={ele}
                      userId={user._id}
                      doBookmark={doBookmark}
                      doUnBookmark={doUnBookmark}
                      onLikeClick={handleLikeClick}
                      onUnLikeClick={handleUnLikeClick}
                      likes={ele.likes ? ele.likes : 0}
                      saves={ele.saves ? ele.saves : 0}
                    />
                  </Pane>
                </Pane>
              </Pane>
            );
          })}
        </div>
      )}
    </div>
  );
}

export default Bookmarks;
