import styled from "styled-components";

export const StyledNavbar = styled.header`
  background: #fff;
  height: 6rem;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 999999;
`;

export const NavOneSide = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const NavOneSideWithMargin = styled(NavOneSide)`
  margin: 0 4rem;
`;

export const ToggleIcon = styled.div`
  font-size: 3rem;
  color: #3f51b5;
  margin: 0 1.5rem;
  cursor: pointer;
  &:hover {
    color: var(--lighter-grey);
    transition: all 0.3s;
  }
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const BrandLogo = styled.a`
  font-family: "Poppins";
  text-transform: uppercase;
  font-size: 2.1rem;
  font-weight: bold;
  color: var(--black);
  text-decoration: none;
`;

export const ProfilePic = styled.div`
  height: 4rem;
  width: 4rem;
  margin-right: 4rem;
  @media screen and (max-width: 600px) {
    display: none;
  }
`;

export const NotifyIcon = styled.div`
  font-size: 2rem;
  color: var(--black);
  position: relative;
  &:first-of-type {
    margin-right: 1rem;
  }
  @media screen and (max-width: 600px) {
    display: none;
  }
`;

export const NotifyCircle = styled.div`
  height: 1.8rem;
  width: 1.8rem;
  background-color: #3f51b5;
  border-radius: 100%;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-family: "Poppins";
  position: absolute;
  top: -0.65rem;
  right: -0.65rem;
  @media screen and (max-width: 600px) {
    display: none;
  }
`;

export const SearchBar = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: "150px";

  @media screen and (max-width: 600px) {
    /* display: none; */
    margin-right: 2rem;
  }
`;

export const SearchIcon = styled.div`
  font-size: 1.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  &:hover {
    color: var(--light-grey);
    cursor: pointer;
  }
`;
export const SearchInputDiv = styled.div`
  display: flex;
  width: ${(props) => !props.openSearch && "0rem"};
  opacity: ${(props) => !props.openSearch && "0"};
  transition: all 0.5s ease-in-out;
`;
export const SearchInput = styled.input`
  border: none;
  border-bottom: 0.15rem solid rgba(0, 0, 0, 0.5);
  font-size: 1.3rem;
  width: ${(props) => !props.openSearch && "0rem"};
  opacity: ${(props) => !props.openSearch && "0"};
  transition: all 0.5s ease-in-out;
  font-family: "Poppins", sans-serif;
  &:focus {
    outline: none;
    border-bottom: 0.15rem solid #3f51b5;
  }
`;

export const MenuBar = styled.div`
  /* margin-top: 15px; */
  background-color: white;
  border-radius: 10px;
  position: "relative";
  top: "0px";
  left: "0px";
`;

export const NavBarCustom = styled.div`
  @media screen and (max-width: 600px) {
    display: none;
  }
`;
