import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles(() => ({
  root: {
    width: "100%",
    "@media screen and (max-width: 750px)": {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      justifyContent: "center",
    },
  },
  stepper: {
    background: "transparent",
    marginTop: "2rem",

    "& button": {
      "& span": {
        fontSize: "1.5rem",
        fontWeight: "bold",
        fontFamily: "Nunito, sans-serif",
        "& span": {
          "& svg": {
            width: "3rem",
            height: "3rem",
            "& text": {
              fontSize: "1.3rem",
            },
          },
        },
      },
    },
  },
  button: {
    marginRight: "1rem",
  },
  backButton: {
    marginRight: "1rem",
  },
  completed: {
    display: "inline-block",
  },
  stepContent: {
    background: "#fff",
    margin: "5rem auto",
    borderRadius: "0.5rem",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    padding: "5rem 0",
    boxShadow: "0rem 1.2rem 2rem rgba(0,0,0,0.2)",
    "& button": {
      fontSize: "1.3rem",
      marginTop: "2rem",
    },
  },
  instructions: {
    marginTop: "1rem",
    marginBottom: "1rem",
    width: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    "& div": {
      width: "100%",
      "& label": {
        left: "3rem",
        fontSize: "1.5rem",
      },
      "& div": {
        width: "100%",
        "& input": {
          margin: "0 3rem",
          fontSize: "1.5rem",
          fontFamily: "Nunito, sans-serif",
        },
        "& fieldset": {
          margin: "0 3rem",
        },
        "& legend": {
          "& span": {
            fontSize: "1.5rem",
          },
        },
      },
    },
  },
  editorStep: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: "#fff",
    padding: "2rem 1rem",
    "& button": {
      fontSize: "1.3rem",
      marginTop: "2rem ",
    },
  },
  editorStepInstructions: {
    display: "block",
    margin: "0",
    padding: "0 1.3rem 5rem 1.3rem",
  },
}));

export default useStyles;
