import React from 'react'
import { Pane, Text } from 'evergreen-ui'
import { Bookmark, Visibility, ThumbUpAlt, ThumbDownAlt } from '@material-ui/icons'
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';
import styles from '../pages/HomePage/home.module.scss'
const PostInsights = (props) => {
    return (
        <Pane className={styles.cardIcons}>
            <Pane className={styles.iconDiv}>
                <Visibility color="primary" fontSize="large" />
                <Text className="ml-2" size={300}>{props.res && props.res.views ? props.res.views : 0}</Text>
            </Pane>
            {props.res && props.res.userSaves && props.res.userSaves.includes(props.userId)
                ?
                <Pane className={styles.iconDiv} onClick={() => props.doUnBookmark(props.res, props.userId)} >
                    <Bookmark color="primary" fontSize="large" />
                    <Text className="ml-2" size={300}>{props.saves}</Text>
                </Pane>
                :
                <Pane className={styles.iconDiv} onClick={() => props.doBookmark(props.res, props.userId)} >
                    <BookmarkBorderIcon color="primary" fontSize="large" />
                    <Text className="ml-2" size={300}>{props.saves}</Text>
                </Pane>
            }
            {props.res && props.res.userLikes && props.res.userLikes.includes(props.userId)
                ?
                <Pane className={styles.iconDiv} onClick={() => props.onUnLikeClick(props.res, props.userId)} >
                    <ThumbDownAlt color="primary" fontSize="large" />
                    <Text className="ml-2" size={300}>{props.likes}</Text>
                </Pane>
                :
                <Pane className={styles.iconDiv} onClick={() => props.onLikeClick(props.res, props.userId)} >
                    <ThumbUpAlt color="primary" fontSize="large" />
                    <Text className="ml-2" size={300}>{props.likes}</Text>
                </Pane>
            }
        </Pane>
    )
}

export default PostInsights;