import React from "react";
import { Navbar, LinkCustom } from "../../styling/BottomNavbarStyles";
import { Link } from "react-router-dom";
import { FaHome, FaUser, FaBell, FaBookmark, FaPen } from "react-icons/fa";
import { MdSettings } from "react-icons/md";

const BottomNavbar = () => {
  return (
    <Navbar>
      <LinkCustom to="/home">
        <FaHome />
      </LinkCustom>
      <LinkCustom to="/profile">
        <FaUser />
      </LinkCustom>
      <LinkCustom to="/publish">
        <FaPen />
      </LinkCustom>
      <LinkCustom to="/notifications">
        <FaBell />
      </LinkCustom>
      <LinkCustom to="/settings">
        <MdSettings />
      </LinkCustom>
    </Navbar>
  );
};

export default BottomNavbar;
