import "./App.css";
import React from "react"
import { Switch, BrowserRouter as Router, Route } from "react-router-dom";
import LandingPage from "./components/pages/LandingPage";
import LogoutPage from "./components/pages/LogoutPage";
import AuthComponent from "./components/AuthComponent";
import HomePage from "./components/pages/HomePage";
import SearchPage from "./components/pages/SearchPage";
import SettingsPage from "./components/pages/SettingsPage";
import CounselmePage from "./components/pages/CounselmePage";
import BookmarksPage from "./components/pages/BookmarksPage";
import FeedbackPage from "./components/pages/FeedbackPage";
import HelpPage from "./components/pages/HelpPage";
import MessagesPage from "./components/pages/MessagesPage";
import NotificationsPage from "./components/pages/NotificationsPage";
import ProfilePage from "./components/pages/ProfilePage";
import UserProfilePage from "./components/pages/UserProfilePage";
import PublishPage from "./components/pages/PublishPage";
import PostdetailsPage from "./components/pages/PostdetailsPage";
import PostdetailsByIdPage from "./components/pages/PostdetailsByIdPage";
import Navbar from "./components/layout/Navbar";
import Sidebar from "./components/layout/Sidebar";
import BottomNavbar from "./components/layout/BottomNavbar";
import { Provider } from "react-redux";
import store from "./redux/store";
import { history } from "./history";
import CategoryBlog from "./components/pages/CategoryBlog";

class App extends React.Component {
  render() {
    return (
      <Provider store={store}>
        <div className="App">
          <Router>
            <Navbar />
            <Sidebar />

          
            
            <Switch>
              <Route exact path="/" component={LandingPage} />
              <Route exact path="/logout" component={LogoutPage} />
              <Route exact path="/auth" component={AuthComponent} />
              <Route exact path="/home" component={HomePage} />
              <Route exact path="/search/:word" component={SearchPage} />
              <Route exact path="/home/:category" component={CategoryBlog} />
              <Route exact path="/settings" component={SettingsPage} />
              <Route exact path="/bookmarks" component={BookmarksPage} />
              <Route exact path="/counselme" component={CounselmePage} />
              <Route exact path="/feedback" component={FeedbackPage} />
              <Route exact path="/help" component={HelpPage} />
              <Route exact path="/messages" component={MessagesPage} />
              <Route exact path="/notifications" component={NotificationsPage} />
              <Route exact path="/profile" component={ProfilePage} />
              <Route exact path="/userprofile/:id" component={UserProfilePage} />
              <Route exact path="/publish" component={PublishPage} />
              <Route exact path="/settings" component={SettingsPage} />
              <Route exact path="/postdetails/" component={PostdetailsPage} />
              <Route
                exact
                path="/postdetails/:id"
                component={PostdetailsByIdPage}
              />
            </Switch>
          </Router>
        </div>
      </Provider>
    );
  }
}

export default App;
