import React, { useEffect, useState } from "react";
import parse from 'html-react-parser';
import { useParams } from "react-router";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { getUser } from "../../../redux/actions/auth";
import { Grid } from "@material-ui/core";
import { Pane, Heading, Text, Paragraph, Badge, Spinner } from "evergreen-ui";
import { Navbar, LinkCustom } from "./searchStyle.js";
import { Link } from "react-router-dom";
import axios from "axios";
import { FaNewspaper, FaUser, FaUsers } from "react-icons/fa";
import styles from "./home.module.scss";
import PostInsights from "../../reusableComponents/PostInsights";
import { proxy } from "../../../utility/proxy";

const moment = require("moment");

function SearchPage(props) {
  console.log("{{{object}}}", props);
  const [isLoading, setIsLoading] = useState(false);
  const [search, setSearch] = useState("");
  const [tab, settab] = useState("post");
  const [userDetails, setUserDetails] = useState([]);
  const [change, handleChange] = useState(true);
  const [postDetails, setPostDetails] = useState([]);
  useEffect(() => {
    if (search.length > 0) {
      axios
        .post(`${proxy}/api/search-users`, { query: search })
        .then((results) => {
          setUserDetails(results.data.user);
        });
      axios
        .post(`${proxy}/api/search-post`, { query: search })
        .then((results) => {
          setPostDetails(results.data.post);
        });
    } else {
      setUserDetails([]);
      setPostDetails([]);
    }
  }, [search]);

  const handleLikeClick = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/like`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };
  const handleUnLikeClick = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/unlike`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };

  const doUnBookmark = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/unSaveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };
  const doBookmark = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/saveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };

  const increaseView = (props, userId) => {
    axios
      .put(`${proxy}/api/increaseView`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
      });
  };

  useEffect(() => {
    setSearch(props.match.params.word);
  }, [props.match.params.word]);

  return (
    <div>
      <Navbar>
        <LinkCustom onClick={() => settab(`post`)}>
          <FaNewspaper />
        </LinkCustom>
        <LinkCustom onClick={() => settab("users")}>
          <FaUsers />
        </LinkCustom>
      </Navbar>
      {tab === "post" ? (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {postDetails &&
            postDetails.length > 0 &&
            postDetails.map((res, i) => {
              let trim = res.description.split(" ");
              trim.length = 30;
              trim = trim.join(" ");
              return (
                <Grid item lg={13} key={i}>
                  <Pane className={styles.card}>
                    <Pane className={styles.cardImage}>
                      <img
                        className={styles.blogImage}
                        src={res.image}
                        alt=""
                      />
                      <Badge color="neutral" className={styles.cardCategory}>
                        {res.category}
                      </Badge>
                    </Pane>
                    <Pane className={styles.cardDescription}>
                      <Badge color="neutral" className={styles.badge}>
                        {res && res.publishedDate
                          ? moment(res.publishedDate).format("DD, MMM YYYY")
                          : "-"}
                      </Badge>
                      <Heading size={600}>{res.heading}</Heading>
                      <Paragraph size={300}>{parse(`${trim}`)}</Paragraph>
                      <Link
                        to={`/postdetails/${res._id}`}
                        onClick={() => increaseView(res, props.user._id)}
                        className={styles.cardButton}
                      >
                        Read More
                      </Link>
                      <Pane className={styles.cardInfo}>
                        <Link
                          className={styles.cardAuthor}
                          to={
                            res.author._id !== props.user._id
                              ? `/userprofile/${res.author._id}`
                              : "/profile"
                          }
                        >
                          <img src="profile.png" alt="" />
                          <Text size={300} paddingLeft={6}>
                            {res.author && res.author.penname
                              ? res.author.penname
                              : "-"}
                          </Text>
                        </Link>
                        <PostInsights
                          res={res}
                          userId={props.user._id}
                          doBookmark={doBookmark}
                          doUnBookmark={doUnBookmark}
                          onLikeClick={handleLikeClick}
                          onUnLikeClick={handleUnLikeClick}
                          likes={res.likes ? res.likes : 0}
                          saves={res.saves ? res.saves : 0}
                        />
                      </Pane>
                    </Pane>
                  </Pane>
                </Grid>
              );
            })}
        </div>
      ) : (
        <div
          style={{
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
            cursor: "pointer",
            padding: "1rem",
          }}
        >
          {userDetails &&
            userDetails.length > 0 &&
            userDetails.map((user, i) => {
              // let trim = res.description.split(" ");
              // trim.length = 30;
              // trim = trim.join(" ");
              return (
                <Link
                  to={
                    user._id !== props.user._id
                      ? `/userprofile/${user._id}`
                      : "/profile"
                  }
                // to={`/userprofile/${user._id}`}
                >
                  <div style={flexRow}>
                  <img
                      style={{
                        height: 50,
                        widht: 50,
                        marginLeft: "30px",
                        marginBottom: "10px",
                        borderRadius: "150px",
                        display: "flex",
                      }} 
                      src={user.proilePic}
                      />
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                      }}
                    >
                      <Heading size={500} style={marginTexts}>
                        @{user.penname}
                      </Heading>
                      <Heading
                        size={700}
                        style={marginTexts}
                        marginBottom="15px"
                      >
                        {user.penname}
                      </Heading>
                    </div>
                  </div>
                </Link>
              );
            })}
        </div>
      )}
    </div>
  );
}

const flexRow = {
  display: "flex",
  flexDirection: "row",
};
const styleProfile = {
  background: "#fff",
  marginLeft: "30px",
  marginBottom: "10px",
  borderRadius: "20px",
  background:
    'url("https://images.pexels.com/photos/937483/pexels-photo-937483.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500")',
  backgroundSize: "cover",
};

const marginTexts = {
  marginLeft: "10px",
  color: "#000",
};
const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};
export default connect(mapStateToProps, { getUser })(SearchPage);
