import React, { useState, useEffect } from "react";
import parse from "html-react-parser";
import {} from "../../../../utils/css/tailwind.css";
import { useSelector } from "react-redux";
import axios from "axios";
import styles from "../../HomePage/home.module.scss";
import { Pane, Heading, Text, Spinner, Paragraph, Badge } from "evergreen-ui";
import { Link } from "react-router-dom";
import PostInsights from "../../../reusableComponents/PostInsights";
import { proxy } from "../../../../utility/proxy";
const moment = require("moment");

function Post(props) {
  const user = useSelector((state) => state.user);
  const [posts, setPosts] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [change, handleChange] = useState(true);

  useEffect(() => {
    setIsLoading(true);
    axios
      .post(`${proxy}/api/user-blog`, { id: user._id })
      .then((res) => {
        setPosts(res.data);
        setIsLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [user, change]);

  const handleLikeClick = (props, userId) => {
    setIsLoading(true);
    axios.put(`${proxy}/api/like`, { _id: props._id, userId }).then((res) => {
      if (res) handleChange(!change);
      setIsLoading(false);
    });
  };
  const handleUnLikeClick = (props, userId) => {
    setIsLoading(true);
    axios.put(`${proxy}/api/unlike`, { _id: props._id, userId }).then((res) => {
      if (res) handleChange(!change);
      setIsLoading(false);
    });
  };

  const doUnBookmark = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/unSaveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };
  const doBookmark = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/saveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };

  const increaseView = (props) => {
    axios.put(`${proxy}/api/increaseView`, { _id: props._id }).then((res) => {
      if (res) handleChange(!change);
    });
  };
  return (
    <div style={{ margin: "0px 40px" }}>
      {isLoading === true ? (
        <Pane
          display="flex"
          alignItems="center"
          justifyContent="center"
          height={600}
        >
          <Spinner color="primary" marginX="auto" />
        </Pane>
      ) : (
        <div>
          {posts.map((res, i) => {
            let trim = res.description.split(" ");
            trim.length = 30;
            trim = trim.join(" ");
            return (
              <Pane className={styles.card}>
                <Pane className={styles.cardImage}>
                  <img
                    className="img-fluid"
                    className={styles.blogImage}
                    src={res.image}
                    alt=""
                  />
                  <Badge color="neutral" className={styles.cardCategory}>
                    {res.category}
                  </Badge>
                </Pane>
                <Pane className={styles.cardDescription}>
                  <Badge color="neutral" className={styles.badge}>
                    {res && res.publishedDate
                      ? moment(res.publishedDate).format("DD, MMM YYYY")
                      : "-"}
                  </Badge>
                  <Heading size={600}>{res.heading}</Heading>
                  <Paragraph size={300}>{parse(`${trim}`)}</Paragraph>
                  <Link
                    to={`/postdetails/${res._id}`}
                    onClick={() => increaseView(res)}
                    className={styles.cardButton}
                  >
                    Read More
                  </Link>
                  <Pane className={styles.cardInfo}>
                    <Pane className={styles.cardAuthor}>
                      <img
                        src={
                          res && res.author && res.author.proilePic
                            ? res.author.proilePic
                            : "https://playuganda.com/wp-content/uploads/2020/12/profile-pic.jpg"
                        }
                        alt=""
                      />
                      <Text size={300} paddingLeft={6} marginRight={60}>
                        {res.author && res.author.penname
                          ? res.author.penname
                          : "-"}
                      </Text>
                    </Pane>
                    <PostInsights
                      res={res}
                      userId={user._id}
                      doBookmark={doBookmark}
                      doUnBookmark={doUnBookmark}
                      onLikeClick={handleLikeClick}
                      onUnLikeClick={handleUnLikeClick}
                      likes={res.likes ? res.likes : 0}
                      saves={res.saves ? res.saves : 0}
                    />
                  </Pane>
                </Pane>
              </Pane>
            );
          })}
        </div>
      )}
    </div>
  );
}

export default Post;
