import axios from "axios";
import { proxy } from "../../utility/proxy";
const signInUser = (data, history) => async (dispatch) => {
  let response = false;
  await axios
    .post(`${proxy}/api/signin`, data)
    .then((res) => {
      response = res.data.code === 200 && res.data.token ? true : false;
      if (res.data.code === 200 && res.data.token) {
        localStorage.setItem("token", res.data.token);
        let config = {
          headers: {
            "x-auth-token": res.data.token,
          },
        };
        axios
          .get(`${proxy}/api/get-user`, config)
          .then((user) => {
            dispatch({
              type: "SAVE_USER",
              payload: user.data,
            });
            if (user.data.isAddedNew) {
              axios.post(
                `${proxy}/api/user/` + user.data._id,
                {
                  isAddedNew: false,
                },
                config
              );
              history.push("/settings");
            } else {
              history.push("/home");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
      return response;
    })
    .catch((e) => {
      console.log(e);
    });
  return response;
};

const signUpUser = (data, history) => async (dispatch) => {
  let response = false;
  await axios
    .post(`${proxy}/api/signup`, data)
    .then((res) => {
      response = res.data.code === 200 && res.data.token ? true : false;
      if (res.data.token) {
        response = true;
        localStorage.setItem("token", res.data.token);
        let config = {
          headers: {
            "x-auth-token": res.data.token,
          },
        };
        axios
          .get(`${proxy}/api/get-user`, config)
          .then((user) => {
            dispatch({
              type: "SAVE_USER",
              payload: user.data,
            });
            history.push("/settings");
          })
          .catch((err) => {
            console.log(err);
          });
      }
    })
    .catch((e) => {
      console.log(e);
    });
  return response;
};

const getUser = (token) => (dispatch) => {
  let config = {
    headers: {
      "x-auth-token": token,
    },
  };
  axios
    .get(`${proxy}/api/get-user`, config)
    .then((user) => {
      dispatch({
        type: "SAVE_USER",
        payload: user.data,
      });
    })
    .catch((err) => {
      console.log(err);
    });
};

export { signInUser, getUser, signUpUser };
