import React, { useEffect, useState } from "react";
import { IoMenuOutline } from "react-icons/io5";
import { FiSearch } from "react-icons/fi";
import { FaTimes } from "react-icons/fa";
import { Redirect } from "react-router-dom";
import { getUser } from "../../redux/actions/auth";
import {
  StyledNavbar,
  NavOneSide,
  ToggleIcon,
  BrandLogo,
  ProfilePic,
  NotifyIcon,
  NotifyCircle,
  SearchBar,
  SearchIcon,
  SearchInput,
  SearchInputDiv,
  NavOneSideWithMargin,
  MenuBar,
  NavBarCustom,
} from "../../styling/NavbarStyles";
import toggleAction from "../../redux/actions/toggleAction";
import { connect, useSelector } from "react-redux";
import axios from "axios";
import { Menu } from "evergreen-ui";
import classNames from "classnames";
import { Link } from "react-router-dom";
import styles from "./headerLinksStyle";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Hidden from "@material-ui/core/Hidden";
import Poppers from "@material-ui/core/Popper";
import Notifications from "@material-ui/icons/Notifications";
import Button from "../CustomButtons/Button.js";
import { proxy } from "../../utility/proxy";
const useStyles = makeStyles(styles);

const Navbar = (props) => {
  let history = useHistory();
  const [openSearch, setOpenSearch] = useState(false);
  const showState = useSelector((state) => state.showSidebar);
  const [search, setSearch] = useState("");
  const [userDetails, setUserDetails] = useState([]);
  const [postDetails, setPostDetails] = useState([]);
  const [notifications, setNotifications] = useState([]);
  const classes = useStyles();
  const [openNotification, setOpenNotification] = React.useState(null);
  const handleClickNotification = (event) => {
    if (openNotification && openNotification.contains(event.target)) {
      setOpenNotification(null);
    } else {
      setOpenNotification(event.currentTarget);
    }
  };
  const handleCloseNotification = () => {
    setOpenNotification(null);
  };
  const handleClick = () => {
    setOpenSearch(!openSearch);
  };
  console.log(notifications);
  useEffect(() => {
    if (props && props.user && props.user._id) {
      axios
        .post(`${proxy}/api/notifications`, {
          userId: props.user._id,
        })
        .then((results) => {
          setNotifications(results.data);
        });
    }
  }, [props]);
  useEffect(() => {
    if (search.length > 0) {
      axios
        .post(`${proxy}/api/search-users`, { query: search })
        .then((results) => {
          setUserDetails(results.data.user);
        });
      axios
        .post(`${proxy}/api/search-post`, { query: search })
        .then((results) => {
          setPostDetails(results.data.post);
        });
    } else {
      setUserDetails([]);
      setPostDetails([]);
    }
  }, [search]);
  const fetchUsers = (query) => {
    setSearch(query);
  };

  if (!localStorage.getItem("token")) {
    return <Redirect to="/" />;
  }
  if (localStorage.getItem("token") && !props.user._id) {
    props.getUser(localStorage.getItem("token"));
  }
  console.log(props.user);
  return (
    <StyledNavbar>
      <NavOneSide>
        <ToggleIcon onClick={() => props.toggleAction(!showState.show)}>
          {showState.show ? <FaTimes /> : <IoMenuOutline />}
        </ToggleIcon>
        <BrandLogo href="/">
          <img
            src="https://glexpace-images.s3.ap-south-1.amazonaws.com/6282b0ed27597811f1aa0fcba61b4bb6.jpg"
            alt="Glexspace"
            style={{ width: "18rem", height: "5rem" }}
          />
          {/* GLEXPACE */}
        </BrandLogo>
      </NavOneSide>
      <NavOneSide>
        <div>
          <div>
            <SearchBar>
              <SearchIcon onClick={handleClick}>
                <FiSearch />
              </SearchIcon>
              <SearchInputDiv openSearch={openSearch}>
                <form
                  onSubmit={(e) => {
                    e.preventDefault();
                    window.open(`/search/${search}`, "_blank");
                  }}
                  style={{ display: "flex", flexDirection: "row" }}
                >
                  <SearchInput
                    style={{ display: "grid" }}
                    type="text"
                    value={search}
                    onChange={(e) => fetchUsers(e.target.value)}
                    openSearch={openSearch}
                  />
                  <SearchIcon>
                    <Link to={`/search/${search}`} target="_blank">
                      <FiSearch />
                    </Link>
                  </SearchIcon>
                </form>
              </SearchInputDiv>
            </SearchBar>
          </div>
          {/* <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "start",
              position: "absolute",
            }}
          >
            {userDetails && userDetails.length > 0 && (
              <MenuBar>
                <Menu>
                  <Menu.Group>
                    {userDetails &&
                      userDetails.map((data, i) => {
                        return (
                          <Menu.Item>
                            <Link
                              onClick={() => {
                                setOpenSearch(false);
                                setSearch("");
                              }}
                              to={
                                data._id !== props.user._id
                                  ? `/userprofile/${data._id}`
                                  : "/profile"
                              }
                            >
                              {data.penname}
                            </Link>
                          </Menu.Item>
                        );
                      })}
                  </Menu.Group>
                </Menu>
              </MenuBar>
            )}
            {postDetails && postDetails.length > 0 && (
              <MenuBar>
                <Menu>
                  <Menu.Group>
                    {postDetails &&
                      postDetails.map((data, i) => {
                        return (
                          <Menu.Item>
                            <Link
                              onClick={() => {
                                setOpenSearch(false);
                                setSearch("");
                              }}
                              to={`/postdetails/${data._id}`}
                            >
                              {data.heading}
                            </Link>
                          </Menu.Item>
                        );
                      })}
                  </Menu.Group>
                </Menu>
              </MenuBar>
            )}
          </div> */}
        </div>
        <NavBarCustom className={classes.manager}>
          <Button
            color={window.innerWidth > 600 ? "transparent" : "white"}
            justIcon={window.innerWidth > 0}
            simple={!(window.innerWidth > 600)}
            aria-owns={openNotification ? "notification-menu-list-grow" : null}
            aria-haspopup="true"
            onClick={handleClickNotification}
            className={classes.buttonLink}
          >
            <Notifications className={classes.icons} />
            <span className={classes.notifications}>
              {notifications.length}
            </span>
            <Hidden mdUp implementation="css">
              <p onClick={handleCloseNotification} className={classes.linkText}>
                Notification
              </p>
            </Hidden>
          </Button>
          <Poppers
            open={Boolean(openNotification)}
            anchorEl={openNotification}
            transition
            disablePortal
            className={
              classNames({ [classes.popperClose]: !openNotification }) +
              " " +
              classes.popperNav
            }
          >
            {({ TransitionProps, placement }) => (
              <Grow
                {...TransitionProps}
                id="notification-menu-list-grow"
                style={{
                  transformOrigin:
                    placement === "bottom" ? "center top" : "center bottom",
                }}
              >
                <Paper style={{ marginRight: "200px" }}>
                  <ClickAwayListener onClickAway={handleCloseNotification}>
                    <MenuList role="menu">
                      {notifications &&
                        notifications.map((data, i) => {
                          return (
                            <MenuItem
                              key={i}
                              onClick={handleCloseNotification}
                              className={classes.dropdownItem}
                            >
                              {data.message}
                            </MenuItem>
                          );
                        })}
                    </MenuList>
                  </ClickAwayListener>
                </Paper>
              </Grow>
            )}
          </Poppers>
        </NavBarCustom>
        {/* <NavOneSideWithMargin>
          <NotifyIcon>
            <BsChat />
            <NotifyCircle>6</NotifyCircle>
          </NotifyIcon>
          <NotifyIcon>
            <VscBell />
            <NotifyCircle>2</NotifyCircle>
          </NotifyIcon>
        </NavOneSideWithMargin> */}
        <ProfilePic onClick={() => history.push(`/profile`)}>
          <img
            src={props.user.proilePic}
            alt={props.user.penname}
            style={{
              borderRadius: "50px",
              height: "100%",
              width: "100%",
            }}
          />
        </ProfilePic>
      </NavOneSide>
    </StyledNavbar>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};
export default connect(mapStateToProps, { getUser, toggleAction })(Navbar);
