import React, { useEffect, useState } from "react";
import parse from "html-react-parser";
import { useParams } from "react-router";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { getUser } from "../../../redux/actions/auth";
import { Grid } from "@material-ui/core";
import { Pane, Heading, Text, Paragraph, Badge, Spinner } from "evergreen-ui";
import styles from "./home.module.scss";
import { Link } from "react-router-dom";
import CategorySlider from "../../reusableComponents/CategorySlider";
import PostInsights from "../../reusableComponents/PostInsights";
import InfiniteScroll from "react-infinite-scroll-component";
import Tags from "../../reusableComponents/Tags";
import axios from "axios";
import BottomNavbar from "../../../components/layout/BottomNavbar";
import { proxy } from "../../../utility/proxy";
const moment = require("moment");

function CategoryBlog(props) {
  const [isLoading, setIsLoading] = useState(false);
  const [postCount, setPostCount] = useState(0);
  const [forYou, setforYou] = useState([]);
  const [change, handleChange] = useState(true);
  const [page, setpage] = useState(0);
  let { category } = useParams();
  const fetchData = (page) => {
    setpage(page);
    if (page === 0) setforYou([])
    var condition;
    category
      ? (condition = {
        currentUserId: props.user && props.user._id,
        categoryPass: category,
        page,
      })
      : condition = {};
    
    !forYou.length && axios
      .post(`${proxy}/api/category-based`, condition)
      .then((res) => {
        setforYou([...forYou, ...res.data]);
        setIsLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    setIsLoading(true);
    axios
      .post(`${proxy}/api/postCount`, {
        currentUserId: props.user._id,
      })
      .then((res) => {
        setPostCount(res.data);
      });
    fetchData(0);
  }, [props.user, category, change]);

  if (!localStorage.getItem("token")) {
    return <Redirect to="/" />;
  }
  if (localStorage.getItem("token") && !props.user._id) {
    props.getUser(localStorage.getItem("token"));
  }

  const handleLikeClick = (props, userId) => {
    setIsLoading(true);
    axios.put(`${proxy}/api/like`, { _id: props._id, userId }).then((res) => {
      if (res) handleChange(!change);
      setIsLoading(false);
    });
  };
  const handleUnLikeClick = (props, userId) => {
    setIsLoading(true);
    axios.put(`${proxy}/api/unlike`, { _id: props._id, userId }).then((res) => {
      if (res) handleChange(!change);
      setIsLoading(false);
    });
  };

  const doUnBookmark = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/unSaveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };
  const doBookmark = (props, userId) => {
    setIsLoading(true);
    axios
      .put(`${proxy}/api/saveBlog`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
        setIsLoading(false);
      });
  };

  const increaseView = (props, userId) => {
    axios
      .put(`${proxy}/api/increaseView`, { _id: props._id, userId })
      .then((res) => {
        if (res) handleChange(!change);
      });
  };

  return (
    <div>
      <BottomNavbar />
      {isLoading === true ? (
        <Pane
          display="flex"
          alignItems="center"
          justifyContent="center"
          height={600}
        >
          <Spinner color="primary" marginX="auto" />
        </Pane>
      ) : (
        <div>
          <CategorySlider />
          <Grid container>
            <Grid container item lg={8} md={12} className={styles.postsGrid}>
              {forYou && forYou.length > 0 && (
                <InfiniteScroll
                  style={{
                    background: "trasparent",
                    width: "115%",
                  }}
                  dataLength={forYou.length ? forYou.length : null}
                  next={() => fetchData(page + 1)}
                  hasMore={true}
                  loader={
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        padding: "10px",
                        fontSize: "18px",
                      }}
                    >
                      Loading...
                    </div>
                  }
                >
                  {forYou.map((res, i) => {
                    let trim = res.description.split(" ");
                    trim.length = 30;
                    trim = trim.join(" ");
                    return (
                      <Grid item lg={13} key={i}>
                        <Pane className={styles.card}>
                          <Pane className={styles.cardImage}>
                            <img
                              className={styles.blogImage}
                              src={res.image}
                              alt=""
                            />
                            <Badge
                              color="neutral"
                              className={styles.cardCategory}
                            >
                              {res.category}
                            </Badge>
                          </Pane>
                          <Pane className={styles.cardDescription}>
                            <Badge color="neutral" className={styles.badge}>
                              {res && res.publishedDate
                                ? moment(res.publishedDate).format(
                                  "DD, MMM YYYY"
                                )
                                : "-"}
                            </Badge>
                            <Heading size={600}>{res.heading}</Heading>
                            <Paragraph size={300}>{parse(`${trim}`)}</Paragraph>
                            <Link
                              to={`/postdetails/${res._id}`}
                              onClick={() => increaseView(res, props.user._id)}
                              className={styles.cardButton}
                            >
                              Read More
                            </Link>
                            {console.log(res.author)}
                            <Pane className={styles.cardInfo}>
                              {
                                res.length ?
                                  <Link
                                    className={styles.cardAuthor}
                                    to={
                                      res.author._id !== props.user._id
                                        ? `/userprofile/${res.author._id}`
                                        : "/profile"
                                    }
                                  >

                                    <img src="profile.png" alt="" />
                                    <Text size={300} paddingLeft={6}>
                                      {res.author && res.author.penname
                                        ? res.author.penname
                                        : "-"}
                                    </Text>
                                  </Link> : null
                              }
                              <PostInsights
                                res={res}
                                // userId={props.user._id}
                                doBookmark={doBookmark}
                                doUnBookmark={doUnBookmark}
                                onLikeClick={handleLikeClick}
                                onUnLikeClick={handleUnLikeClick}
                                likes={res.likes ? res.likes : 0}
                                saves={res.saves ? res.saves : 0}
                              />
                            </Pane>
                          </Pane>
                        </Pane>
                      </Grid>
                    );
                  })}
                </InfiniteScroll>
              )}
            </Grid>

            <Grid container item lg={4} md={12} className={styles.profileGrid}>
              <Link to="/publish" className={styles.publishLink}>
                Publish Your Story
              </Link>
              <Pane className={styles.profileCard}>
                <Pane className={styles.profileCardImg}>
                  {props.user && props.user.proilePic ? (
                    <img src={props.user.proilePic} alt={props.user.penname} />
                  ) : (
                    <img src="profile.png" alt="" />
                  )}
                </Pane>
                <Pane className={styles.profileCardName}>
                  <Heading color="white">
                    {props.user && props.user.penname}
                  </Heading>
                  {/* <Text color="white">UI Designer</Text> */}
                </Pane>
                <Pane className={styles.profileCardCounts}>
                  <Pane className={styles.profileCardCountsItem}>
                    <Heading color="white" size={600}>
                      {postCount && postCount}
                    </Heading>
                    <Text color="#fafafa" size={300}>
                      Posts
                    </Text>
                  </Pane>
                  <Pane className={styles.profileCardCountsItem}>
                    <Heading color="white" size={600}>
                      {props.user &&
                        props.user.subscribers &&
                        props.user.subscribers.length}
                    </Heading>
                    <Text color="#fafafa" size={300}>
                      Subscribers
                    </Text>
                  </Pane>
                  <Pane className={styles.profileCardCountsItem}>
                    <Heading color="white" size={600}>
                      {props.user &&
                        props.user.subscribers &&
                        props.user.subscribe.length}
                    </Heading>
                    <Text color="#fafafa" size={300}>
                      Subscribes
                    </Text>
                  </Pane>
                </Pane>
              </Pane>
              <Tags />
            </Grid>
          </Grid>
        </div>
      )}
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};
export default connect(mapStateToProps, { getUser })(CategoryBlog);
