import React, { Fragment, useState, useEffect } from "react";
import { Pane, Text, Heading, Badge, Spinner } from "evergreen-ui";
import { AiFillInstagram, AiOutlineTwitter, AiFillYoutube } from "react-icons/ai";
import { FaFacebookSquare, FaLinkedin } from "react-icons/fa";
import Paper from "@material-ui/core/Paper";
import Tabs from "@material-ui/core/Tabs";
import { Tab, Grid, Divider } from "@material-ui/core";
import classes from "./profile.module.css";
import Post from "./childComponents/Posts";
import Bookmarks from "./childComponents/Bookmarks";
import ForYou from "./childComponents/ForYou";
import { getUser } from "../../../redux/actions/auth";
import { connect, useSelector } from "react-redux";
import { proxy } from "../../../utility/proxy";
import BottomNavbar from "../../layout/BottomNavbar";
function ProfilePage(props) {
  const user = useSelector((state) => state.user);
  if (localStorage.getItem("token") && !user._id) {
    props.getUser(localStorage.getItem("token"));
  }
  const [value, setValue] = useState(0);
  const [tab, setTab] = useState(<ForYou />);
  const [isLoading, setIsLoading] = useState(false);
  const [innerWidth, setinnerWidth] = useState(window.innerWidth);

  window.addEventListener("resize", function () {
    setinnerWidth(window.innerWidth);
    // console.log(window.innerWidth);
  });

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const colors = ["default", "error", "primary", "secondary"];
  const getRandomColor = () => {
    return colors[Math.floor(Math.random() * colors.length)];
  };

  return (
    <div>
      <BottomNavbar />
      <Pane
        style={{
          background: "linear-gradient(#93a5cf ,#e4efe9)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
        height={220}
        width="100%"
        border="default"
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "end",
          }}
        >
          <div style={flexRow}>
            <img
              style={{
                height: 80,
                widht: 80,
                marginLeft: "30px",
                marginBottom: "10px",
                borderRadius: "50px",
                display: "flex",
              }}
              src={user.proilePic}
            ></img>
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
              }}
            >
              <Heading size={500} style={marginTexts}>
                @{user.penname}
              </Heading>
              <Heading size={700} style={marginTexts} marginBottom="15px">
                {user.penname}
              </Heading>
            </div>
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
            }}
          >
            <div style={flexColumn}>
              <Text size={400} style={secondaryText}>
                Subscribes
              </Text>
              <Text size={600} style={marginForVars}>
                {user && user.subscribe && user.subscribe.length}
              </Text>
            </div>
            <div style={flexColumn}>
              <Text size={400} style={secondaryText}>
                Views
              </Text>
              <Text size={600} style={marginForVars}>
                {user && user.views}
              </Text>
            </div>
            <div style={flexColumn}>
              <Text size={400} style={secondaryText}>
                Subscribers
              </Text>
              <Text size={600} style={marginForVars}>
                {user && user.subscribers && user.subscribers.length}
              </Text>
            </div>
          </div>
        </div>
      </Pane>

      <div>
        <Paper
          style={{ backgroundColor: "rgb(240 243 247)", boxShadow: "none" }}
        >
          <Tabs
            value={value}
            onChange={handleChange}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            <Tab onClick={() => setTab(<ForYou />)} label="For you" />
            <Tab onClick={() => setTab(<Post />)} label="Posts" />
            <Tab onClick={() => setTab(<Bookmarks />)} label="Bookmarks" />
          </Tabs>
        </Paper>

        <div
          style={
            innerWidth <= 1100
              ? {
                  display: "flex",
                  margin: "20px",
                  paddingBottom: "27px",
                  flexDirection: "column",
                  justifyContent: "center",
                }
              : {
                  display: "flex",
                  margin: "20px",
                  paddingBottom: "27px",
                  justifyContent: "center",
                }
          }
        >
          {tab}
          <div
            className={classes.desktopContent}
            className="pt-10 rounded mr-10"
            style={flexColumn}
          >
            <Pane
              borderRadius={15}
              padding={2}
              elevation={4}
              float="left"
              width={"100%"}
              maxWidth={"350px"}
              minWidth={"350px"}
              display="flex"
              flexDirection="column"
              background="white"
            >
              <Text size={600} className={classes.textsec}>
                Social Profiles
              </Text>
              <div>
                {/* {user &&
                  user.skills &&
                  user.skills.map((item, index) => {
                    return (
                      <Badge key={index} color={() => getRandomColor()}>
                        {item}
                      </Badge>
                    );
                  })} */}
                  <div style={{ marginBottom: 20, display: 'flex' }}>
                    <AiFillInstagram onClick={() => window.open(user && user.instagram, '_blank').focus()} style={{ fontSize: '25px', marginLeft: 15 }} />
                    <FaLinkedin onClick={() => window.open(user && user.linkedin, '_blank').focus()} style={{ fontSize: '25px', marginLeft: 15 }} />
                    <AiFillYoutube onClick={() => window.open(user && user.youtube, '_blank').focus()} style={{ fontSize: '25px', marginLeft: 15 }} />
                    <AiOutlineTwitter onClick={() => window.open(user && user.twitter, '_blank').focus()} style={{ fontSize: '25px', marginLeft: 15 }} />
                  </div>
              </div>
            </Pane>

            {/* <Pane
              borderRadius={15}
              padding={2}
              elevation={4}
              float="left"
              width={350}
              marginRight={24}
              marginTop={20}
              display="flex"
              flexDirection="column"
              background="white"
            >
              <Text size={600} className={classes.textsec}>Social Profiles</Text>
              <div style={{ marginBottom: 20, display: 'flex' }}>
                <AiFillInstagram style={{ fontSize: '25px', marginLeft: 15 }} />
                <FaLinkedin style={{ fontSize: '25px', marginLeft: 15 }} />
                <FaFacebookSquare style={{ fontSize: '25px', marginLeft: 15 }} />
                <AiOutlineTwitter style={{ fontSize: '25px', marginLeft: 15 }} />
              </div>
            </Pane> */}
          </div>
        </div>
      </div>
    </div>
  );
}

const flexRow = {
  display: "flex",
  flexDirection: "row",
};

const flexColumn = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
};

const marginTexts = {
  marginLeft: "10px",
  color: "#000",
};

const marginForVars = {
  marginLeft: "20px",
  color: "#000",
  textAlign: "center",
  fontWeight: "bold",
};

const secondaryText = {
  color: "rgb(43 43 43)",
  marginLeft: "30px",
  fontWeight: "bold",
};
const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};
export default connect(mapStateToProps, { getUser })(ProfilePage);
