import styled from "styled-components";
export const SidebarWrapper = styled.div`
  z-index: 9999;
  position: fixed;
  top: 1rem;
  left: ${(props) => (props.show ? "0" : "-23rem")};
  transition: all 0.3s ease-in-out;
  background: #222222;
  height: 100%;
  width: 23rem;
  color: #fff;
  font-family: "Nunito", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 5rem;
`;

export const Backdrop = styled.div`
  z-index: 9998;
  position: fixed;
  top: 5rem;
  left: ${(props) => (props.show ? "0" : "-150vw")};
  transition: all 0.3s ease-in-out;
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  height: 150vh;
  width: 150vw;
  color: #fff;
  font-family: "Nunito", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 5rem;
`;

export const SidebarItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 1.5rem;
  color: #fff;
  text-decoration: none;
  padding-bottom: 1.5rem;
`;

export const SidebarItemIcon = styled.div`
  font-size: 2rem;
  z-index: 3;
`;

export const SidebarItemName = styled.div`
  font-size: 1.3rem;
  margin-left: 5rem;
`;

export const SidebarVerticalStrip = styled.div`
  position: fixed;
  top: 0;
  height: 100%;
  width: 5rem;
  background-color: #000000;
  opacity: 0.6;
  z-index: 2;
`;
