import "./style.css";
import React, { useState } from "react";
import { Dialog } from 'evergreen-ui'
import registerimg from "./img/register.svg";
import log from "./img/log.svg";

import { connect } from "react-redux";
import { signInUser, signUpUser } from "../../redux/actions/auth";
import { SelectMenu, TextInput , Spinner, toaster, Button } from "evergreen-ui";
import axios from "axios";
import { proxy } from "../../utility/proxy";

function AuthComponent(props) {
  if (localStorage.getItem("token")) {
    props.history.push("/settings");
  }
  const [isSignUpSuccess, setisSignUpSuccess] = useState(false);
  const [modeToggle, setmodeToggle] = useState(true);
  const [isShown, setIsShown] = React.useState(false)
  const [stateLogin, setStateLogin] = useState({
    penname: "",
    password: "",
  });
  const [stateSignUp, setStateSignUp] = useState({
    penname: "",
    email: "",
    password: "",
    categories: [],
  });
  const handleLogin = (e) => {
    e.preventDefault();
    setisLoginClicked(true);
    if (!stateLogin.penname) {
      toaster.danger("Required penname!");
    } else if (!stateLogin.password) {
      toaster.danger("Required password!");
    }
    if (stateLogin.penname && stateLogin.password) {
      props.signInUser(stateLogin, props.history).then((res) => {
        if (!res) {
          toaster.danger("Invalid Credentials!");
        } else {
          toaster.success("Login Suuccessfully!");
        }

      });
    }
    setisLoginClicked(false);
  };
  const handleOnChange = (e) =>
    setStateLogin({ ...stateLogin, [e.target.name]: e.target.value });

  const handleSignUp = (e) => {
    e.preventDefault();
    setisSignUpSuccess(true);
    if (!stateSignUp.penname) {
      toaster.danger("Required Username!");
    } else if (!stateSignUp.email) {
      toaster.danger("Required email!");
    } else if (!stateSignUp.password) {
      toaster.danger("Required password!");
    } else {
      props
        .signUpUser(
          { ...stateSignUp, categories: selectedItemsState },
          props.history
        )
        .then((res) => {
          if (!res) {
            toaster.danger("Commination with that penname and email already exist! Please try again..");
          }
        });
    }
    setisSignUpSuccess(false);
  };
  const handleSgnUpOnChange = (e) => {
    console.log(e.target.value)
    setStateSignUp({ ...stateSignUp, [e.target.name]: e.target.value });
  }

  const [options] = React.useState(
    [
      "Programming",
      "Network",
      "Marketing",
      "digital",
      "algorithm",
      "Politics",
      "Entertainment",
      "Sports",
      "Geography",
      "Religion",
      "stocks",
      "social",
      "program",
      "covid",
      "UN",
      "tips",
      "Trip",
    ].map((label) => ({
      label,
      value: label,
    }))
  );
  const [selectedItemsState, setSelectedItems] = React.useState([]);
  const [selectedItemNamesState, setSelectedItemNames] = React.useState(null);
  const [isLoginClicked, setisLoginClicked] = useState(false);
  const [forgotEmail,  setForgotEmail] = useState(null);

  const handleForgotPassword = () => {
    if(forgotEmail) {
      axios.post(`${proxy}/api/send-forgot-mail`, {
        email: forgotEmail
      }).then(res => {
        alert(res.data.message);
      })
      .catch(err => {
        alert("Something went wrong!");
      });
    }
  }

  return (
    <div className={modeToggle ? "container" : "container sign-up-mode"}>
      <div className="forms-container">
        <div className="signin-signup">

          <Dialog
            isShown={isShown}
            title="Forgot password"
            onCloseComplete={() => setIsShown(false)}
            hasFooter={false}
          >
            <span><TextInput onChange={(e) => setForgotEmail(e.target.value)} name="text-input-name" placeholder="Enter penname" /> <Button onClick={handleForgotPassword}>Send Link</Button> </span>
          </Dialog>


          <form onSubmit={handleLogin} className="sign-in-form">
            <h2 className="title">Login</h2>
            <div className="input-field">
              <i className="fas fa-user"></i>
              <input
                type="text"
                name="penname"
                onChange={handleOnChange}
                placeholder="penname"
              />
            </div>
            <div className="input-field">
              <i className="fas fa-lock"></i>
              <input
                type="password"
                onChange={handleOnChange}
                name="password"
                placeholder="Password"
              />
            </div>
            <a onClick={() => setIsShown(true)}>Forgot Password?</a>
            <input
              type="submit"
              value={isLoginClicked ? <Spinner size={20} /> : "Login"}
              disabled={isLoginClicked}
              className="btn solid"
            />
            {/* <p className="social-text">Or Sign in with social platforms</p>
            <div className="social-media">
              <a href='/' className='social-icon'>
                <i className='fab fa-facebook-f'></i>
              </a>
              <a href='/' className='social-icon'>
                <i className='fab fa-twitter'></i>
              </a>
              <a href="/" className="social-icon">
                <i className="fab fa-google"></i>
              </a>
              <a href="/" className="social-icon">
                <i className="fab fa-linkedin-in"></i>
              </a>
            </div> */}
          </form>

          <form onSubmit={handleSignUp} className="sign-up-form">
            <h2 className="title">Register</h2>
            <div className="input-field">
              <i className="fas fa-user"></i>
              <input
                type="text"
                placeholder="Username"
                onChange={handleSgnUpOnChange}
                name="penname"
              />
            </div>
            <div className="input-field">
              <i className="fas fa-envelope"></i>
              <input
                type="email"
                placeholder="Email"
                name="email"
                onChange={handleSgnUpOnChange}
              />
            </div>
            <div className="input-field">
              <i className="fas fa-lock"></i>
              <input
                type="password"
                placeholder="Password"
                onChange={handleSgnUpOnChange}
                name="password"
              />
            </div>
            {/* <div className="input-field">
              <i className="fab fa-linkedin"></i>
              <input
                type="linkedin"
                placeholder="Linked In"
                onChange={handleSgnUpOnChange}
                name="linkedin"
              />
            </div>
            <div className="input-field">
              <i className="fab fa-twitter"></i>
              <input
                type="twitter"
                placeholder="Twitter"
                onChange={handleSgnUpOnChange}
                name="twitter"
              />
            </div>
            <div className="input-field">
              <i className="fab fa-youtube"></i>
              <input
                type="youtube"
                placeholder="Youtube"
                onChange={handleSgnUpOnChange}
                name="youtube"
              />
            </div>
            <div className="input-field">
              <i className="fab fa-instagram"></i>
              <input
                type="instagram"
                placeholder="Instagram"
                onChange={handleSgnUpOnChange}
                name="instagram"
              />
            </div> */}
            <SelectMenu
              className="select-field"
              isMultiSelect
              style={{ width: "100%" }}
              title="Select multiple names"
              options={options}
              selected={selectedItemsState}
              onSelect={(item) => {
                const selected = [...selectedItemsState, item.value];
                const selectedItems = selected;
                const selectedItemsLength = selectedItems.length;
                let selectedNames = "";
                if (selectedItemsLength === 0) {
                  selectedNames = "";
                } else if (selectedItemsLength === 1) {
                  selectedNames = selectedItems.toString();
                } else if (selectedItemsLength > 1) {
                  selectedNames =
                    selectedItemsLength.toString() + " selected...";
                }
                setSelectedItems(selected);
                setSelectedItemNames(selectedNames);
              }}
              onDeselect={(item) => {
                const deselectedItemIndex = selectedItemsState.indexOf(
                  item.value
                );
                const selectedItems = selectedItemsState.filter(
                  (_item, i) => i !== deselectedItemIndex
                );
                const selectedItemsLength = selectedItems.length;
                let selectedNames = "";
                if (selectedItemsLength === 0) {
                  selectedNames = "";
                } else if (selectedItemsLength === 1) {
                  selectedNames = selectedItems.toString();
                } else if (selectedItemsLength > 1) {
                  selectedNames =
                    selectedItemsLength.toString() + " selected...";
                }

                setSelectedItems(selectedItems);
                setSelectedItemNames(selectedNames);
              }}
            >
              <input
                className="btn custom-button pl-10"
                value={selectedItemNamesState || "Select category"}
              ></input>
            </SelectMenu>
            {isSignUpSuccess ? (
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  color: "white",
                }}
              >
                <Spinner
                  size={24}
                  style={{ color: "#FFFFFF" }}
                  iconColor="white"
                />
              </div>
            ) : (
              <button
                type="submit"
                className="btn"
                placeholder="Sign up"
                disabled={isSignUpSuccess}
              >
                Register{" "}
              </button>
            )}
            {/* <p className="social-text">Or Sign up with social platforms</p>
            <div className="social-media">
              <a href="/" className="social-icon">
                <i className="fab fa-facebook-f"></i>
              </a>
              <a href="/" className="social-icon">
                <i className="fab fa-twitter"></i>
              </a>
              <a href="/" className="social-icon">
                <i className="fab fa-google"></i>
              </a>
              <a href="/" className="social-icon">
                <i className="fab fa-linkedin-in"></i>
              </a>
            </div> */}
          </form>
        </div>
      </div>

      <div className="panels-container">
        <div className="panel left-panel">
          <div className="content">
            <h3>New here ?</h3>
            <p>
              Register with your favourite Topics in a few seconds. Learn and then Earn with us.
            </p>
            <button
              className="btn transparent"
              id="sign-up-btn"
              onClick={() => setmodeToggle(false)}
            >
              Register
            </button>
          </div>
          <img src={log} className="image" alt="" />
        </div>
        <div className="panel right-panel">
          <div className="content">
            <h3>One of us ?</h3>
            <p>
              Lovely! You showed that you are a real Writer, Login to help others with your writings.
            </p>
            <button
              className="btn transparent"
              id="sign-in-btn"
              onClick={() => setmodeToggle(true)}
            >
              Login
            </button>
          </div>
          <img src={registerimg} className="image" alt="" />
        </div>
      </div>
    </div>
  );
}
export default connect(null, { signInUser, signUpUser })(AuthComponent);
